/* eslint-disable import/no-extraneous-dependencies, @typescript-eslint/no-explicit-any */
import React from 'react';
import styled, { keyframes } from 'styled-components';
import Tour from 'reactour';
import {
  FireIcon,
  PointerIcon,
  SnowIcon,
  WaterIcon
} from '../../../components/icon/index';
import { StartMode } from '../../../assets/const';


const moveLeftAnimation = keyframes`
  0% {
    left: -30%;
  }
  100% {
    left: 30%;
  }
`;

const moveRightAnimation = keyframes`
  0% {
    left: 30%;
  }
  100% {
    left: -30%;
  }
`;

const DragRightIcon = styled(PointerIcon)`
  animation-name: ${moveLeftAnimation};
  left: -30%;
  color: #3260a8;
`;

const DragLeftIcon = styled(PointerIcon)`
  animation-name: ${moveRightAnimation};
  left: 30%;
  color: #3260a8;
`;

const StyledFireIcon = styled(FireIcon)`
  color: rgb(250, 42, 27);
  font-size: 30px;
`;

const StyledSnowIcon = styled(SnowIcon)`
  color: rgb(92, 138, 255);
  font-size: 30px;
`;

const GuideWrapper = styled.div`
  font-size: ${props => `calc(${props.theme.font.sm}*0.7)`};
`;

const CommandWrapper = styled.div`
  font-size: ${props => `calc(${props.theme.font.sm}*0.7)`};
  display: flex;
  flex-direction: column;
  text-align: left;
`;

const DD = styled.dd`
  margin-left: 15%;
`;

// ================= Specify Guide Steps =================
/**
 * @selector wapper className of your target
 * @content put your guide components here
 * @position |"top"|"bottom"|"left"|"right"|[number, number] 
 * 
 * visit https://reactour.js.org/ for more detail
 */
const STEPS = [
  {
    selector: '.temperature-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <GuideWrapper>
        <br />
        <StyledFireIcon />
        {inDOM && 'Drag right to increase temperature!'}
        <br />
        <DragRightIcon />
      </GuideWrapper>
    ),
    position: 'top' as 'top',
  },
  {
    selector: '.temperature-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <GuideWrapper>
        <br />
        <StyledSnowIcon />
        {inDOM && 'Drag left to decrease temperature!'}
        <br />
        <DragLeftIcon />
      </GuideWrapper>
    ),
    position: 'top' as 'top',
  },
  {
    selector: '.water-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <GuideWrapper>
        <br />
        <WaterIcon level="high" />
        {inDOM && 'Drag right to increase water volume!'}
        <br />
        <DragRightIcon />
      </GuideWrapper>
    ),
    position: 'top' as 'top',
  },
  {
    selector: '.water-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <GuideWrapper>
        <br />
        <WaterIcon level="off" />
        {inDOM && 'Drag left to decrease water volume!'}
        <br />
        <DragLeftIcon />
      </GuideWrapper>
    ),
    position: 'top' as 'top',
  }
];

const TimeSTEP = {
  selector: '.time-start-btn-wapper',
  content: ({ inDOM }: { inDOM: any }) => (
    <GuideWrapper>
      <br />
      {inDOM && 'Time countedown, click to start shower!'}
      <br />
    </GuideWrapper>
  ),
  position: 'top' as 'top',
};

const VolumeSTEP = {
  selector: '.volume-start-btn-wapper',
  content: ({ inDOM }: { inDOM: any }) => (
    <GuideWrapper>
      <br />
      {inDOM && 'Volume countedown, click to start shower!'}
      <br />
    </GuideWrapper>
  ),
  position: 'top' as 'top',
};

const VoiceSTEP = {
  selector: '.voice-btn-wapper',
  content: ({ inDOM }: { inDOM: any }) => (
    <CommandWrapper>
      <br />
      <h3>{inDOM && 'Click to turn on voice command!'}</h3>
      <dl>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'increase by x'
        </dt>
        <DD> - increase xC</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'decrease by x'
        </dt>
        <DD> - decrease xC</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'hotter'
        </dt>
        <DD> - increase 1C</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'cooler' or 'colder'
        </dt>
        <DD> - decrease 1C</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'stop'
        </dt>
        <DD> - stop voice detection</DD>
      </dl>
    </CommandWrapper>
  ),
  position: 'top' as 'top',
};

interface Props {
  showVoiceCommand: boolean;
  isOpen: boolean;
  toggleGuide: (nextValue?: any) => void;
  startMode: StartMode;
}

function Guide({
  showVoiceCommand,
  isOpen,
  toggleGuide,
  startMode
}: Props) {
  const steps = STEPS.slice();

  startMode === StartMode.Time
    ? steps.push(TimeSTEP)
    : steps.push(VolumeSTEP);
    
  if (showVoiceCommand) steps.push(VoiceSTEP);

  return (
    <Tour
      steps={steps}
      isOpen={isOpen}
      onRequestClose={toggleGuide}
    />
  );
}

export default Guide;