/* eslint-disable no-underscore-dangle */
import http from './http';

export default class UserService {
  static api = 'users';

  static async getByClient(id: number):
  Promise<[undefined, UserProfile[]] | [MyError, undefined]> {
    const request = {
      url: `${UserService.api}/client/${id}`,
      requireAuth: true
    };
    try {
      const res = await http(request);
      const data: UserProfile[] = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async add(user: UserProfile):
  Promise<[undefined, UserProfile] | [MyError, undefined]> {
    const request = {
      url: `${UserService.api}/`,
      options: {
        method: 'POST',
        body: JSON.stringify(user),
      },
      requireAuth: true
    };
    try {
      const res = await http(request);
      const data: UserProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async update(user: UserProfile):
  Promise<[undefined, UserProfile] | [MyError, undefined]> {
    const request = {
      url: `${UserService.api}/${user._id}`,
      options: {
        method: 'PUT',
        body: JSON.stringify(user),
      },
      requireAuth: true
    };
    try {
      const res = await http(request);
      const data: UserProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async delete(id: number):
  Promise<[undefined, UserProfile] | [MyError, undefined]> {
    const request = {
      url: `${UserService.api}/${id}`,
      options: {
        method: 'DELETE',
      },
      requireAuth: true
    };
    try {
      const res = await http(request);
      const data: UserProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }
}
