/* eslint-disable no-throw-literal, @typescript-eslint/no-throw-literal */
import store from 'store';
import { API, StoreKeys } from '../assets/const';

const http = (request: {
  url: string,
  options?: RequestInit,
  requireAuth?: boolean
}) => {
  const headers: { [headerName: string]: string } = {
    'Content-Type': 'application/json'
  };

  const composedOptions = request.options
    ? { headers, ...request.options }
    : { headers };

  if (request.requireAuth) {
    (composedOptions.headers as Dictionary<string>).Authorization
      = `Bearer ${store.get(StoreKeys.token)}`;
  }

  return fetch(API.BASE_API + request.url, composedOptions)
    .then((response) => {
      if (!response.ok) {
        throw ({ code: response.status, error: response.statusText });
      }
      return response;
    });
};

export default http;