/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useRef } from 'react';
import { Carousel } from 'antd';
import ProfileSider from './profileSider/profileSider';
import ProfileSection from './profileSections/clientProfileSection';
import UserProfileSection from './profileSections/userProfileSection';
import './index.less';

const ProfilePage = () => {
  const carouseEl = useRef(null);

  return (
    <div className='profile-page-wrapper'>
      <ProfileSider
        goTo={(index: number) => {
          (carouseEl as React.MutableRefObject<any>).current.goTo(index);
        }}
      />
      <Carousel
        infinite={false}
        dotPosition='bottom'
        ref={carouseEl}
        swipe={false}
      >
        <ProfileSection />
        <UserProfileSection />
      </Carousel>
    </div>
  );
};

export default ProfilePage;
