import React, { useContext, useCallback } from 'react';
import styled from 'styled-components';
import { Input } from 'antd';
import { observer } from 'mobx-react';
import { ProfileStoreCtx } from '../../store/profileStore';
import { WidgetSearchCancelBtn } from '../styled/index';
import { API } from '../../assets/const';

const StyledSearch = styled(Input.Search)`
  position: relative;
  width: 100%;
  height: ${props => props.addonBefore ? '60px' : '40px'};
  .ant-input-group-addon {
    font-size: 25px;
    font-weight: bold;
    color: gray;
    width: 100px;
    height: inherit;
    padding: 0;
    border: none;
  }
  .ant-input {
    font-size: 25px;
  }
  .ant-input-wrapper{
    height: inherit;
  }
`;

const PlayListWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: auto;
  height: calc(100 % - 40px);
`;

const PlayListTitle = styled.h1`
  color: white;
  margin: 0;
`;

const CollectionWrapper = styled.div`
  position: relative;
  margin: 5px;
  height: 100px;
  width: 100%;
  overflow: hidden;
  text-align: justify;
  img {
    width: 100px !important;
    display: inline-block !important;
    vertical-align: top;
  }
  h3 {
    color: white;
    display: inline-block;
    width: calc(100% - 100px);
    overflow: hidden;
    vertical-align: bottom;
    padding: 10px;
  }
  cursor: pointer;
`;

interface PlayListWrapperProp {
  fullSize: boolean;
}

const ResultWrapper = styled.div<PlayListWrapperProp>`
  position: absolute;
  width: 100%;
  z-index: 1;
  background: rgba(0, 0, 0, 0.4);
  top: ${props => props.fullSize ? '60px' : '40px'};
  height: ${props => props.fullSize ? 'calc(100vh - 40px)' : 'calc(100% - 40px)'};
  overflow: auto;
`;

interface Props {
  addonBefore?: React.ReactNode | undefined | string;
}

const SearchVideo = observer(({ addonBefore }: Props) => {
  const profileStore = useContext(ProfileStoreCtx);

  const renderResult = useCallback((videoResults: VideoResult[]) => {
    if (videoResults.length <= 0) return null;
    return (
      <ResultWrapper fullSize={!!addonBefore}>
        <PlayListTitle> Select Your Video
          <WidgetSearchCancelBtn onClick={profileStore.clearVideoResults}>
            X
          </WidgetSearchCancelBtn>
        </PlayListTitle>
        <PlayListWrapper>
          {videoResults.map(
            (video: VideoResult) => {
              return (
                <CollectionWrapper
                  key={video.id?.videoId}
                  onClick={e => {
                    e.stopPropagation();
                    profileStore.setVideo(
                      video.id?.videoId,
                    );
                  }}
                >
                  <img
                    src={
                      `${API.YOUTUBE_IMG_API + video.id?.videoId}/0.jpg`
                    }
                    alt='artwork'
                  />
                  <h3>{video.snippet.title}</h3>
                </CollectionWrapper>
              );
            }
          )}
        </PlayListWrapper>
      </ResultWrapper>
    );
  }, [profileStore, addonBefore]);

  return (
    <>
      <StyledSearch
        onSearch={(value: string) => {
          profileStore.searchVideo(value);
        }}
        addonBefore={addonBefore}
        disabled={profileStore.isVideoLoading}
        placeholder='Search Video Track'
      />
      {renderResult(profileStore.videoResults)}
    </>
  );
});

export default SearchVideo;
