/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useContext, useEffect, useState } from 'react';
import { observer } from 'mobx-react';
import { Card, Menu } from 'antd';
import styled from 'styled-components';
import { DefaultBtn } from '../../../../components/styled';
import { avatars } from '../../../../assets/img/index';
import { SignUpForm, LoginForm, ResetPasswordForm } from '../../../../components/forms';


import { ProfileStoreCtx } from '../../../../store/profileStore';
import { composeIconUrl } from '../../../../utils';


const { Meta } = Card;

const LogoutBtn = styled(DefaultBtn)`
`;

const Wrapper = styled.div`
  height: ${props => props.theme.content.height};
  width: ${props => `calc(${props.theme.app.minWidth}*0.25)`};
  background-color: white;
  box-shadow: 4px 4px 8px 4px rgba(0,0,0,0.2);
  .ant-menu-item {
    font-size: ${props => `calc(${props.theme.font.sm}*0.8)`};
    font-weight: 500;
    color: gray;
  }
  .ant-card-meta-title {
    font-size: ${props => `calc(${props.theme.font.sm}*0.7)`};
  }
  .ant-card-meta-description {
    font-size: ${props => `calc(${props.theme.font.sm}*0.7)`};
  }
`;

interface Props {
  goTo: any;
}

const ProfileSider = ({ goTo }: Props) => {
  const profileStore = useContext(ProfileStoreCtx);
  const [index, setIndex] = useState(0);

  useEffect(() => {
    profileStore.checkSession();
  }, [profileStore]);

  useEffect(() => {
    goTo(index);
  }, [goTo, index]);

  const renderLogin = () => {
    return (
      <Card
        cover={
          <img className="logo" src={avatars['1']} alt='avatar' />
        }
      >
        <Meta
          description="Welcome"
        />
        <SignUpForm />
        <LoginForm />
      </Card>
    );
  };

  const renderSider = (clientProfile: ClientProfile) => {
    return (
      <>
        <Card
          cover={
            <img
              className="logo"
              src={avatars[clientProfile.iconId]
                || composeIconUrl(clientProfile.iconId)}
              alt='avatar'
            />
          }
        >
          <Meta
            title={clientProfile.client}
            description="Welcome"
          />
          <LogoutBtn onClick={profileStore.logout}>Logout</LogoutBtn>
          <ResetPasswordForm />
        </Card>

        <Menu
          defaultSelectedKeys={[index.toString()]}
          mode="inline"
        >
          <Menu.Item key="0" onClick={() => setIndex(0)}>Client Profile</Menu.Item>
          <Menu.Item key="1" onClick={() => setIndex(1)}>User Profiles</Menu.Item>
        </Menu>
      </>
    );
  };

  return (
    <Wrapper>
      {profileStore.isLogin
        ? renderSider(profileStore.clientProfile as ClientProfile)
        : renderLogin()}
    </Wrapper>
  );
};

export default observer(ProfileSider);
