import React, { useContext, useState, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { Input, message } from 'antd';
import {
  EditOutlined,
} from '@ant-design/icons';
import { observer } from 'mobx-react';
import Section from '../../../../components/section/section';
import { SectionHeader } from '../../../../components/styled';
import SearchMusic from '../../../../components/musicWidget/searchMusic';
import SearchVideo from '../../../../components/videoWidget/searchVideo';
import AvatarPicker from '../../../../components/avatarPicker/avatarPicker';
import PecsManagement from '../../../../components/pecsManagement';
import { ProfileStoreCtx } from '../../../../store/profileStore';
import { placeholder } from '../../../../assets/img/index';
import { API } from '../../../../assets/const';
import { clientNameValidator } from '../../../../components/forms/validators';

const Wrapper = styled.div`
  position: relative;
  img {
    margin:0;
    width: 100%;
    height: 100%;
  }
  .ant-radio-group{
    height: 100%;
    width: 100%;
  }
  .ant-radio-button-wrapper {
    padding-bottom: 6%;
    width: 6%;
  }
`;

const StyledInput = styled(Input)`
  width: 100%;
  height: 60px;
  .ant-input-group-addon {
    height: 60px;
    font-size: 24px;
    font-weight: bold;
    color: white;
    background: rgb(59,59,59);
    width: 150px;
    border: none;
    span {
      font-size: 25px;
    }
  }
  .ant-input {
    font-size: ${props => `calc(${props.theme.font.md}*0.7)`};
    height: 60px;
  }
`;

const rotateAnimation = keyframes`
  0% {transform: rotate3d(0, 1, 0, 0deg);}
  50% {transform: rotate3d(0, 1, 0, 180deg);}
  100% {transform: rotate3d(0, 1, 0, 360deg);}
`;

const CurrentCollectionCover = styled.img`
  position: relative;
  margin: 2px;
  width: 100%;
  height: 100%;
  box-shadow: 0px 2px 2px 2px rgba(0,0,0,0.2);
  animation: ${rotateAnimation} 2s linear;
  background: rgba(0,0,0,0.5);
`;

const validateClientName = (clientName: string): Promise<boolean> => {
  return clientNameValidator(undefined, clientName)
    .then(() => true)
    .catch(err => {
      message.warn(err);
      return false;
    });
};

const ClientProfileSection = () => {
  const profileStore = useContext(ProfileStoreCtx);
  const client = profileStore.clientProfile?.client;
  const [clientName, setClientName]
    = useState(client);

  useEffect(() => {
    setClientName(client);
  }, [client]);

  const renderProfile = () => {
    return (
      <Wrapper>
        <Section flexDirection="column">
          <SectionHeader>
            <h1>Icons</h1>
          </SectionHeader>
          <AvatarPicker
            imageId={profileStore.clientProfile?.iconId}
            handleChange={(iconId: string) => {
              profileStore.updateClientIcon(iconId, () => { });
            }}
          />
        </Section>
        <Section flexDirection="row">
          <StyledInput
            addonBefore="Client Name"
            value={clientName}
            onChange={e => {
              setClientName(e.target.value);
            }}
            addonAfter={
              <EditOutlined onClick={async () => {
                if (!clientName) return;
                if (await validateClientName(clientName))
                  profileStore.updateClientName(clientName);
              }}
              />
            }
          />
        </Section>
        <Section flexDirection="column">
          <SectionHeader>
            <h1>Auido Track</h1>
          </SectionHeader>
          <SearchMusic
            addonBefore={
              <CurrentCollectionCover
                src={profileStore.currentAudio?.cover
                  ? profileStore.currentAudio.cover
                  : placeholder.soundtrack}
                alt='artwork'
              />
            }
          />
        </Section>
        <Section flexDirection="column">
          <SectionHeader>
            <h1>Video Track</h1>
          </SectionHeader>
          <SearchVideo
            addonBefore={
              <CurrentCollectionCover
                src={
                  `${API.YOUTUBE_IMG_API +
                  profileStore.currentVideo?.id
                  }/0.jpg`
                }
                alt='artwork'
              />
            }
          />
        </Section>
        <Section flexDirection="column">
          <PecsManagement />
        </Section>
      </Wrapper>
    );
  };

  return (
    profileStore.isLogin
      ? renderProfile()
      : null
  );
};

export default observer(ClientProfileSection);
