import React from 'react';
import { ClockCircleOutlined } from '@ant-design/icons';
import styled from 'styled-components';
import Timer from 'react-compound-timer';
import { ShowerState } from '../../assets/const';

interface AtHeader {
  atHeader: boolean;
}

const Wrapper = styled.div<AtHeader>`
  position: relative;
  height: ${props => props.atHeader ? '100%' : `calc(${props.theme.font.sm}*3)`};
  width: ${props => props.atHeader ? '100%' : `calc(${props.theme.font.sm}*3 )`};
  font-weight: ${props => props.atHeader ? '700' : '400'};
  color: white;
  transition: all 0.5s linear;
  z-index: 1;
  cursor: pointer;
  > span {
    position: absolute;
    font-size: ${props => props.theme.font.sm};
    z-index: 3;
    color: white;
    transform: translateX(-50%);
    span {
      font-size: ${props => props.theme.font.sm};
    }
  }
`;

interface PieProps {
  angle: number;
}

const Pie = styled.div<PieProps & AtHeader>`
  position: absolute;
  --angle: ${props => `${props.angle}deg`};
	border-radius: ${props => props.atHeader ? '0%' : '25%'};
  background: conic-gradient(transparent var(--angle), #434343 var(--angle));
  height: 100%;
  width: 100%;
  z-index: 2;
`;

const PieShadow = styled.div<AtHeader>`
  background: rgba(0, 0, 0, 0.4);
  background: ${props => props.atHeader ? 'gray' : 'rgba(0, 0, 0, 0.4);'};
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: ${props => props.atHeader ? '0%' : '25%'};
  z-index: 1;
`;

function TimeCountDown({
  active,
  timer,
  timerCountdown,
  atHeader,
  showerState,
  setShowerState
}: {
  timer: TimerRenderProps;
  timerCountdown: number;
  atHeader: boolean;
  active: boolean;
} & ShowerControl<ShowerState>) {
  return (
    <Timer.Consumer>
      {() => {
        return (
          <Wrapper
            className={atHeader ? '' : 'time-start-btn-wapper'}
            atHeader={atHeader}
            onClick={() => {
              // disable start button on header
              if (atHeader || !active) return;

              // start timer
              if (showerState === ShowerState.Inited) {
                timer.start();
                setShowerState(ShowerState.Started);
              }
            }}
          >
            {/* Count down pie animation container */}
            <Pie
              atHeader={atHeader}
              angle={
                timerCountdown === 0
                  ? 0
                  : (1 - parseInt(timer.getTime(), 10) / timerCountdown) * 360
              }
            />

            {/* Count down pie shadow */}
            <PieShadow atHeader={atHeader} />

            {/* Time display) */}
            <span>
              <Timer.Minutes />:<Timer.Seconds />
              <ClockCircleOutlined style={{ display: 'block' }} />
            </span>
          </Wrapper>
        );
      }}
    </Timer.Consumer>
  );
}

export default TimeCountDown;
