/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useContext } from 'react';
import styled, { ThemeContext } from 'styled-components';
import { Slider } from 'antd';

import { CONST } from '../../assets/const';

const TemperaturePickerWrapper = styled.div`
`;

// props are injected from Antd Form
function TemperaturePicker(props: any) {
  const theme = useContext<AppTheme>(ThemeContext);
  
  // customize marks on the slider
  const marks = {
    [CONST.MIN_TEMP]: {
      style: {
        color: '#91d5ff',
        fontSize: `calc(${theme.font.sm}*0.8)`
      },
      label: <strong>{CONST.MIN_TEMP}C</strong>,
    },
    [CONST.MAX_TEMP]: {
      style: {
        color: '#f50',
        fontSize: `calc(${theme.font.sm}*0.8)`
      },
      label: <strong>{CONST.MAX_TEMP}C</strong>,
    },
  };

  return (
    <TemperaturePickerWrapper>
      <Slider
        {...props}
        range
        marks={marks}
        min={CONST.MIN_TEMP}
        max={CONST.MAX_TEMP}
        defaultValue={[CONST.MIN_TEMP, CONST.MAX_TEMP]}
        initialValue={[CONST.MIN_TEMP, CONST.MAX_TEMP]}
      />
    </TemperaturePickerWrapper>
  );
}

export default TemperaturePicker;
