/* eslint-disable no-underscore-dangle */
import http from './http';

export default class ImageService {
  static api = 'image';

  static async add(formData: FormData, headers: Dictionary<string>):
  Promise<[undefined, Icon] | [MyError, undefined]> {
    const request = {
      url: `${ImageService.api}`,
      options: {
        method: 'POST',
        body: formData,
        headers
      },
      requireAuth: true
    };

    try {
      const res = await http(request);

      const data: Icon = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async update(formData: FormData, headers: Dictionary<string>):
  Promise<[undefined, Icon] | [MyError, undefined]> {
    const request = {
      url: `${ImageService.api}`,
      options: {
        method: 'PUT',
        body: formData,
        headers
      },
      requireAuth: true
    };

    try {
      const res = await http(request);
      const data: Icon = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }
}
