import React, { useEffect, useState, useRef, useContext } from 'react';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { ProfileStoreCtx } from '../../../store/profileStore';
import { useToggle } from '../../../hooks/index';
import TimeCountdown from '../../../components/countDown/timeCountDown';
import VolumeCountdown from '../../../components/countDown/volumeCountDown';
import TemperatureSlider from '../../../components/temperatureSlider/temperatureSlider';
import WaterSlider from '../../../components/waterSlider/waterSlider';
import VoiceControlBtn from '../../../components/voiceControlBtn/voiceControlBtn';
import Menu from './temperatureMenu';
import { StartMode, ShowerState, CONST } from '../../../assets/const';

const TemperaturePageWrapper = styled.div`
  background-color: white;
  height: ${props => props.theme.content.height};
  min-width: ${props => props.theme.app.minWidth};
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  position: relative;
`;

const CountDownPicker = styled.div`
  position: relative;
  height: ${props => `calc(${props.theme.font.sm}*3)`};
  width: ${props => `calc(${props.theme.font.sm}*3 )`};
  > h1 {
    position: relative;
    margin-top: 0px;
    span {
      transition: all 0.5s linear;
      font-size:  ${props => `calc(${props.theme.font.sm}*1 )`};
    }
  }
`;

const PickerBtn = styled.button`
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 2;
  top:0;
  color: ${props => props.disabled ? 'lightgray' : ''};
  font-size: ${props => `calc(${props.theme.font.lg} * 0.9)`};
  background: transparent;
  border: none;
  cursor: pointer;
  &:focus {
    outline: none;
  }
  transition: all 0.5s linear;
`;

const PickerLeft = styled(PickerBtn)`
    left: -100%;
`;

const PickerRight = styled(PickerBtn)`
    right: -100%;
`;

const PickerInner = styled.div`
  height: ${props => `calc(${props.theme.font.sm}*3)`};
  width: ${props => `calc(${props.theme.font.sm}*3*2.5)`};
  display: flex;
  transition: all 0.5s linear;
`;

const PickerDisplay = styled.div`
  height: ${props => `calc(${props.theme.font.sm}*3)`};
  width: ${props => `calc(${props.theme.font.sm}*3)`};  
  overflow: hidden; 
`;

function TemperaturePage({
  easy,
  startMode,
  setStartMode,
  timer,
  timerCountdown,
  finished,
  setVolume,
  volume,
  volumeCountdown,
  showerState,
  setShowerState,
  setTimerCountdown,
  setVolumeCountdown
}: {
  easy: boolean;
  startMode: StartMode;
  setStartMode: React.Dispatch<React.SetStateAction<StartMode>>;
  finished: boolean;
  setTimerCountdown: React.Dispatch<React.SetStateAction<number>>;
  setVolumeCountdown: React.Dispatch<React.SetStateAction<number>>;
} & VolumeCountdownControl & TimeCountdownControl & ShowerControl<ShowerState>
) {
  const profileStore = useContext(ProfileStoreCtx);
  const { currentUser } = profileStore;
  const minTemp = currentUser
    ? currentUser.temperatureRange[0]
    : CONST.MIN_TEMP;
  const maxTemp = currentUser
    ? currentUser.temperatureRange[1]
    : CONST.MAX_TEMP;
  const [temperature, setTemperature] =
    useState<number>(Math.floor((minTemp + maxTemp) / 2));
  const [showVoiceCommand, toggleShowVoiceCommand] = useToggle(true);
  const pickerInner = useRef<null | HTMLDivElement>(null);

  useEffect(() => {
    setTemperature(Math.floor((minTemp + maxTemp) / 2));
  }, [minTemp, maxTemp]);

  const pickerMove = (direction: 0 | -1) => {
    if (pickerInner.current) {
      pickerInner.current.style.transform = `translateX(${direction * 40}%)`;
      if (direction === -1) {
        setStartMode(StartMode.Volume);
      } else {
        setStartMode(StartMode.Time);
      }
    }
  };

  return (
    <TemperaturePageWrapper>
      <Menu
        showVoiceCommand={showVoiceCommand}
        toggleShowVoiceCommand={toggleShowVoiceCommand}
        startMode={startMode}
      />
      <TemperatureSlider
        easy={easy}
        temperature={temperature}
        setTemperature={setTemperature}
        minTemp={minTemp}
        maxTemp={maxTemp}
      />
      <WaterSlider easy={easy} finished={finished} />
      <CountDownPicker>
        <PickerDisplay>
          <PickerLeft
            disabled={
              startMode === StartMode.Time ||
              showerState !== ShowerState.Inited
            }
            style={{
              opacity: showerState === ShowerState.Inited ? 1 : 0
            }}
            onClick={() => pickerMove(0)}
          >
            {'<'}
          </PickerLeft>
          <PickerInner ref={pickerInner}>
            <TimeCountdown
              active={startMode === StartMode.Time}
              timer={timer}
              timerCountdown={timerCountdown}
              atHeader={false}
              showerState={showerState}
              setShowerState={setShowerState}
            />
            <VolumeCountdown
              active={startMode === StartMode.Volume}
              internval={CONST.VOLUME_CHECK_INTERNVAL}
              easy={easy}
              setVolume={setVolume}
              volume={volume}
              volumeCountdown={volumeCountdown}
              showerState={showerState}
              setShowerState={setShowerState}
              atHeader={false}
            />
          </PickerInner>
          <PickerRight
            disabled={
              startMode === StartMode.Volume ||
              showerState !== ShowerState.Inited
            }
            style={{
              opacity: showerState === ShowerState.Inited ? 1 : 0
            }}
            onClick={() => pickerMove(-1)}
          >
            {'>'}
          </PickerRight>
        </PickerDisplay>
        <h1>
          <span
            style={{
              opacity: showerState === ShowerState.Inited ? 1 : 0,
              fontSize: '1.5rem'
            }}
          >
            Start
          </span>
        </h1>
      </CountDownPicker>
      {showVoiceCommand &&
        <VoiceControlBtn
          minTemp={minTemp}
          maxTemp={maxTemp}
          easy={easy}
          temperature={temperature}
          setTemperature={setTemperature}
          setVolume={setVolume}
          volume={volume}
          showerState={showerState}
          startMode={startMode}
          timer={timer}
          setTimerCountdown={setTimerCountdown}
          setVolumeCountdown={setVolumeCountdown}
        />}
    </TemperaturePageWrapper>
  );
}

export default observer(TemperaturePage);
