import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import { AutoComplete } from 'antd';
import { observer } from 'mobx-react';
import { PecsStoreCtx } from '../../store/pecsStore';
import { fuzzyStringRank, composeIconUrl } from '../../utils';

const { Option } = AutoComplete;

// used to split pecs name and current input value to make option unique
const splitSymbol = '^';

const PecsWrapper = styled.img`
  height: 100px;
  width: 100px;
`;

const PecsPreview = styled.img`
  height: 40px;
  width: 40px;
  margin: 10px 0;
`;

const Description = styled.p`
  color: gray;
  font-weight: 500;
  font-size: 20px;
  padding-top: 10px;
`;

interface Props {
  value: string;
  pecs: TodoPecs | null;
  setPecs: React.Dispatch<React.SetStateAction<TodoPecs | null>>;
  setValue: React.Dispatch<React.SetStateAction<string>>;
}

// this element is outside the scope
// config this class in app.css
const dropdownClassName = 'auto-complete-dropdown';

function ToDoAutoComplete({
  value,
  pecs,
  setPecs,
  setValue
}: Props) {
  const [result, setResult] = useState<TodoPecs[]>([]);
  const pecsStore = useContext(PecsStoreCtx);
  const { pecsArr } = pecsStore;
  const handleSearch = (input: string) => {
    if (pecs) setPecs(null);

    // rank input value with pecs' keys by leven distance
    const rankedPecsArr = fuzzyStringRank(input, pecsArr);
    const composedPecsArr = rankedPecsArr.map(p => {
      return {
        value: p.name + splitSymbol + input, // combined value -> "name^input"
        name: p.name, // name of pecs
        imageId: p.imageId // image src of pecs
      };
    });

    setResult(composedPecsArr);
  };

  const handleSelect = (input: string) => {
    const [n, v] = input.split(splitSymbol);
    setPecs({
      value: v, // combined value -> "name^input"
      name: n, // name of pecs
      imageId: pecsArr.filter(p => p.name === n)[0].imageId // image src of pecs
    });
  };

  return (
    <>
      <AutoComplete
        onSearch={handleSearch}
        placeholder="e.g. Take a shower"
        onSelect={handleSelect}
        onChange={(input: string) => {
          // "pecs^input" -> parts[0] -> pecs
          // "pecs^input" -> parts[1] -> input
          const parts = input.split((splitSymbol));

          // exclude pecs from combined value
          setValue(parts[1] ? parts[1] : parts[0]);
        }}
        value={value}
        dropdownClassName={dropdownClassName}
      >
        {result.map((curPecs: TodoPecs) => (
          <Option key={curPecs.name} value={curPecs.value}>
            <PecsWrapper src={composeIconUrl(curPecs.imageId)} />
            <Description>{curPecs.name}</Description>
          </Option>
        ))}
      </AutoComplete>
      {
        pecs
        &&
        <PecsPreview
          src={composeIconUrl(
            pecsArr.filter(p => p.name === pecs.name)[0].imageId
          )}
        />
      }
    </>
  );
}

export default observer(ToDoAutoComplete);
