/* eslint-disable no-underscore-dangle */
import React, { useContext } from 'react';
import styled from 'styled-components';
import { Modal, Card } from 'antd';
import { observer } from 'mobx-react';
import { ProfileStoreCtx } from '../../store/profileStore';
import useToggle from '../../hooks/useToggle';
import { avatars, appIcons } from '../../assets/img/index';
import { ShowerState } from '../../assets/const';
import { composeIconUrl } from '../../utils';

const UserSelectBtn = styled.img`
  position: absolute;
  border: none;
  font-size: 1.4rem;
  left: 230px;
  width: ${props => props.theme.header.height};
  font-weight: bold;
  height: 100%;
  &:focus {
    outline: 0 !important;
  }
  cursor: pointer;
  filter: drop-shadow(5px 5px 5px #222);
  padding: 5px;
`;

const { Meta } = Card;

const UserCard = styled(Card)`
  margin: 0px 5px 15px 5px;
`;

const Wrapper = styled.div`
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
`;

const Avatar = styled.img`
  height: 150px;
  width: 150px;
`;

function UserSelector({
  showerState,
  setShowerState
}: ShowerControl<ShowerState>) {
  const profileStore = useContext(ProfileStoreCtx);
  const [visible, toggleVisible] = useToggle(false);

  const selectUser = (userID: number) => {
    setShowerState(ShowerState.Inited);
    profileStore.setCurrentUser(userID);
    toggleVisible();
  };

  const handleCancel = () => {
    toggleVisible();
  };

  const renderUsers = (): React.ReactNode => {
    const { clientProfile } = profileStore;
    if (!clientProfile 
      || !clientProfile.users || clientProfile.users.length === 0) return null;

    return clientProfile.users.map(user =>
      <UserCard
        hoverable
        key={user._id}
        style={{ width: 150 }}
        cover={
          <Avatar
            alt="icon"
            src={avatars[user.iconId]
              || composeIconUrl(user.iconId)}
          />
}
        onClick={() => selectUser(user._id as number)}
      >
        <Meta title={user.name} />
      </UserCard>
    );
  };

  return (
    <>
      <UserSelectBtn
        onClick={() => {
          if (showerState === ShowerState.Started) return;
          toggleVisible();
        }}
        src={profileStore.currentUser
          ? avatars[profileStore.currentUser.iconId]
          || composeIconUrl(profileStore.currentUser.iconId)
          : appIcons.anonymous}
      />
      <Modal
        title="Who is this showering?"
        visible={visible}
        onCancel={handleCancel}
        footer={[]}
        width={800}
        style={{ fontSize: '50px' }}
      >
        <Wrapper>
          {renderUsers()}
          <UserCard
            hoverable
            style={{ width: 150 }}
            cover={<Avatar alt="icon" src={appIcons.anonymous} />}
            onClick={() => selectUser(-1)}
          >
            <Meta title='Anonymous' />
          </UserCard>
        </Wrapper>
      </Modal>
    </>
  );
}

export default observer(UserSelector);
