import React from 'react';
import styled from 'styled-components';
import { Layout, Button } from 'antd';
import {
  CaretLeftOutlined,
  CaretRightOutlined
} from '@ant-design/icons';

const Footer = styled(Layout.Footer)`
  min-width: ${props => props.theme.app.minWidth};
  display: inline-block;
  height: ${props => props.theme.footer.height};
  background-color: white;
  
  .back-btn {
    float: left;
    background-color: transparent;
    border: 0;

    span {
      color: black;
      font-size: 18px;
      vertical-align: top;
    }

    :disabled {
      span {
        color: lightgray;
      }
    }
  }

  .next-btn {
    float: right;
    background-color: transparent;
    border: 0;

    span {
      color: black;
      font-size: 18px;
      vertical-align: top;
    }

    :disabled {
      span {
        color: lightgray;
      }
    }
  }
`;

/** 
 * @prev scroll app to the left 
 * 
 * @next scroll app to the right  
 */
interface AppFooterProps {
  prev: (index: number) => void;
  next: (index: number) => void;
  index: number;
}

const INDEX_RANGE = [0, 2];

function AppFooter(props: AppFooterProps) {
  const { prev, next, index } = props;
  return (
    <Footer>
      <Button
        className='back-btn'
        onClick={() => { prev(index); }}
        disabled={index <= INDEX_RANGE[0]}
      >
        <CaretLeftOutlined style={{ color: 'black', fontSize: 30 }} />
        <span> BACK </span>
      </Button>

      <Button
        className='next-btn'
        onClick={() => { next(index); }}
        disabled={index >= INDEX_RANGE[1]}
      >
        <span> NEXT </span>
        <CaretRightOutlined style={{ color: 'black', fontSize: 30 }} />
      </Button>
    </Footer>
  );
}

export default AppFooter;
