import store from 'store';
import http from './http';
import { StoreKeys } from '../assets/const';

interface Return {
  token: string,
  message: string,
  client: ClientProfile
}

export default class AuthenticationService {
  static api = 'authenticate';

  static async checkSession(
  ): Promise<[undefined, ClientProfile] | [MyError, undefined]> {
    const request = {
      url: `${AuthenticationService.api}/session`,
      requireAuth: true
    };
    try {
      const res = await http(request);
      const data: ClientProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async signUp(
    client: {
      client: string,
      password: string,
      iconId: string
    }
  ): Promise<[undefined, ClientProfile] | [MyError, undefined]> {
    const request = {
      url: `${AuthenticationService.api}/signup`,
      options: {
        method: 'POST',
        body: JSON.stringify(client),
      }
    };
    try {
      const res = await http(request);
      const data: ClientProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async login(
    credentials: {
      client: string,
      password: string
    }
  ): Promise<[undefined, Return] | [MyError, undefined]> {
    const request = {
      url: `${AuthenticationService.api}/login`,
      options: {
        method: 'POST',
        body: JSON.stringify(credentials),
      }
    };
    try {
      const res = await http(request);
      const data: Return = await res.json();
      store.set(StoreKeys.token, data.token);
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static logout() {
    store.remove(StoreKeys.token);
  }
}