/* eslint-disable no-underscore-dangle */
import React, { useContext, useEffect } from 'react';
import styled from 'styled-components';
import { Card } from 'antd';
import {
  DeleteOutlined
} from '@ant-design/icons';
import { observer } from 'mobx-react';
import { PecsStoreCtx } from '../../store/pecsStore';
import { SectionHeader } from '../styled';
import { PecsForm } from '../forms';
import { composeIconUrl } from '../../utils';


const { Meta } = Card;

const PecsManageContent = styled.div`
  display: flex;
  flex-wrap: wrap;
  .ant-card-cover {
    height: 300px;
  }
  .ant-card-body {
    padding: 18px;
  }
`;

function PecsManagement() {
  const pecsStore = useContext(PecsStoreCtx);
  const { pecsArr } = pecsStore;
  const onDelete = async (pecs: Pecs) => {
    await pecsStore.removePecs({ id: pecs._id, imageId: pecs.imageId });
  };

  useEffect(() => {
    pecsStore.getPecs();
  }, [pecsStore]);

  return (
    <>
      <SectionHeader>
        <h1> Pecs Management </h1>
        <PecsForm />
      </SectionHeader>
      <PecsManageContent>
        {pecsArr.map(pecs =>
          <Card
            hoverable
            style={{
              width: 'calc(20% - 10px)',
              height: '400px',
              margin: '5px 5px 5px 5px'
            }}
            cover={
              <img
                alt="pecs"
                src={composeIconUrl(pecs.imageId)}
                style={{
                  height: '100%'
                }}
              />
            }
            key={pecs._id}
            actions={[
              <PecsForm
                id={pecs._id}
                imageId={pecs.imageId}
                name={pecs.name}
                key="pecs"
              />,
              <DeleteOutlined
                key="delete"
                onClick={() => onDelete(pecs)}
              />
            ]}
          >
            <Meta title={pecs.name} />
          </Card>
        )}
      </PecsManageContent>
    </>
  );
}

export default observer(PecsManagement);
