/* eslint-disable no-underscore-dangle, @typescript-eslint/indent */
import { observable, action } from 'mobx';
import { createContext } from 'react';
import { message } from 'antd';
import { API } from '../assets/const';
import { shouldApplyDemoStore } from '../utils';
import AuthService from '../services/Authentication';
import UserService from '../services/User';
import ClientService from '../services/Client';
import DemoProfileStore from './profileStore.demo';


class ProfileStore {
  /*
   * Profile Related Attributes and Functions
   */
  @observable
  clientProfile: ClientProfile | null = null;

  @observable
  selectedUserProfile: UserProfile | undefined | null
    = this.clientProfile?.users[0];

  @observable
  isLogin: boolean = false;

  @observable
  currentUser: UserProfile | undefined | null
    = null;

  @observable
  isLoading: boolean = false;

  @observable
  isAudioLoading: boolean = false;

  @observable
  isVideoLoading: boolean = false;

  // once login, fetch client's users
  private static async getUsersData(clientId: number):
    Promise<undefined | UserProfile[]> {
    const [err, userProfiles] = await UserService.getByClient(clientId);
    if (!userProfiles || err) {
      message.error(err ? err.error : 'Unexpected Error');
      return undefined;
    }
    return userProfiles;
  }

  // reset password
  public static async resetPassword(newPassword: string):
    Promise<boolean> {
    const [err, msg] = await ClientService.resetPassword({
      password: newPassword
    });
    if (!msg || err) {
      message.error(err ? err.error : 'Unexpected Error');
      return false;
    }
    message.success(msg.message);
    return true;
  }

  // used in both checkSession and login action
  @action.bound
  async onLogin(clientProfile: ClientProfile) {
    this.currentAudio = clientProfile.audio;
    this.currentVideo = clientProfile.video;

    // get sub users's data
    let usersData
      = await ProfileStore.getUsersData(clientProfile._id);
    if (!usersData) usersData = [];

    // eslint-disable-next-line no-param-reassign
    clientProfile.users = usersData;
    if (usersData.length > 0) this.setSelectedUserProfile(usersData[0]);

    this.clientProfile = clientProfile;
    this.isLogin = true;
  }

  // once app is loaded, check if there is an existing valid session to skip login
  @action.bound
  async checkSession() {
    const [err, clientProfile] = await AuthService.checkSession();

    if (!clientProfile || err) {
      this.logout();
      return false;
    }

    this.onLogin(clientProfile);
    return true;
  }

  // Sign Up
  @action.bound
  async signUp(client: { client: string, password: string, iconId: string }) {
    const [err, clientData] = await AuthService.signUp(client);
    if (!clientData || err) {
      if (err?.code === 409) {
        message.error('Client name already exist');
      } else {
        message.error(err ? err.error : 'Failed to signup');
      }
      return false;
    }
    this.isLogin = false; // just to use this (make it a non-static method)
    return true;
  }

  // Login
  @action.bound
  async login(credentials: { client: string, password: string }) {
    // get client data
    const [err, clientData] = await AuthService.login(credentials);
    if (!clientData || err) {
      message.error(err ? err.error : 'Not Authorized');
      return false;
    }

    this.onLogin(clientData.client);
    return true;
  }

  // Logout
  @action.bound
  logout() {
    AuthService.logout();
    this.clientProfile = null;
    this.isLogin = false;
  }

  // select one of the users as current shower user 
  @action.bound
  setCurrentUser(userID: number) {
    [this.currentUser] = (this.clientProfile as ClientProfile).users
      .filter(user => userID === user._id);
  }

  // update client - name
  @action.bound
  async updateClientName(client: string) {
    const [err, clientProfile]
      = await ClientService.update({ client });
    if (!clientProfile || err) {
      if (err?.code === 401) this.logout();
      message.error(err ? err.error : 'Unexpected Error');
    } else {
      (this.clientProfile as ClientProfile).client = client;
      message.success('Profile has been updated');
    }
  }

  // update client - icon
  @action.bound
  async updateClientIcon(iconId: string, callback: () => void) {
    const [err, clientProfile]
      = await ClientService.update({ iconId });
    if (!clientProfile || err) {
      if (err?.code === 401) this.logout();
      message.error(err ? err.error : 'Unexpected Error');
    } else {
      (this.clientProfile as ClientProfile).iconId = iconId;
      callback();
      message.success('Client Icon has been updated');
    }
  }

  // update client - add user
  @action.bound
  async addUserProfile(userProfile: UserProfile) {
    const [err, addedUserProfile] = await UserService.add(userProfile);
    if (!addedUserProfile || err) {
      if (err?.code === 401) this.logout();
      message.error(err ? err.error : 'Unexpected Error');
    } else {
      // select added user
      this.setSelectedUserProfile(addedUserProfile);
      (this.clientProfile as ClientProfile).users.unshift(addedUserProfile);
      message.success('Profile has been added');
    }
  }

  // update user
  @action.bound
  async updateUserProfile(userProfile: UserProfile) {
    const [err, updatedUserProfile] = await UserService.update(userProfile);
    if (!updatedUserProfile || err) {
      if (err?.code === 401) this.logout();
      message.error(err ? err.error : 'Unexpected Error');
    } else {
      const targetProfile = (this.clientProfile as ClientProfile).users.find(
        user => user._id === userProfile._id
      );

      if (targetProfile) {
        targetProfile.name = updatedUserProfile.name;
        targetProfile.dob = updatedUserProfile.dob;
        targetProfile.iconId = updatedUserProfile.iconId;
        targetProfile.fColor = updatedUserProfile.fColor;
        targetProfile.fVideo = updatedUserProfile.fVideo;
        targetProfile.fTemperature = updatedUserProfile.fTemperature;
        targetProfile.temperatureRange = updatedUserProfile.temperatureRange;
        targetProfile.timer = updatedUserProfile.timer;
        targetProfile.volume = updatedUserProfile.volume;
        targetProfile.medicalNotes = updatedUserProfile.medicalNotes;
      } else {
        message.error('Profile not exist');
      }

      message.success('Profile has been updated');
    }
  }

  // update client - remove user
  @action.bound
  async removeUserProfile(id: number) {
    const [err, deletedUserProfile] = await UserService.delete(id);
    if (!deletedUserProfile || err) {
      if (err?.code === 401) this.logout();
      message.error(err ? err.error : 'Unexpected Error');
    } else {
      (this.clientProfile as ClientProfile).users
        = (this.clientProfile as ClientProfile).users.filter(
          user => user._id !== id
        );

      // select first user
      this.setSelectedUserProfile(this.clientProfile?.users[0]);
      message.success('Profile has been removed');

      // clear selected user if it is deleted
      if (this.currentUser?._id === id) this.currentUser = null;
    }
  }

  // update client - video
  @action.bound
  async setVideo(id: string) {
    this.currentVideo = { id };
    const video: Video = { id };
    const [err, clientProfile]
      = await ClientService.update({ video });

    if (!clientProfile || err) {
      message.error(err ? err.error : 'Unexpected Error');
      if (err?.code === 401) {
        this.logout();
        return;
      }
    } else {
      message.success('Video updated');
    }

    (this.clientProfile as ClientProfile).video = { id };
    this.clearVideoResults();
  }

  // update client - audio
  @action.bound
  async setAudioCollection(id: number, cover: string) {
    this.currentAudio = { id, cover };
    const audio: Audio = {
      id,
      cover
    };
    const [err, clientProfile]
      = await ClientService.update({ audio });
    if (!clientProfile || err) {
      message.error(err ? err.error : 'Unexpected Error');
      if (err?.code === 401) {
        this.logout();
        return;
      }
    } else {
      message.success('Audio updated');
    }
    (this.clientProfile as ClientProfile).audio
      = { id, cover };
    this.clearAudioCollection();
  }

  // select one of the user profile - only one could be selected
  @action.bound
  setSelectedUserProfile(userProfile: UserProfile | null | undefined) {
    this.selectedUserProfile = userProfile;
  }

  /*
   * Audio/Video Related Attributes and Functions
   */
  @observable
  currentAudio: { id: number, cover: string } | undefined
    = this.clientProfile?.audio;

  @observable
  currentVideo: { id: string } | undefined
    = this.clientProfile?.video;

  @observable
  audioCollections: AudioCollection[] = [];

  @observable
  videoResults: VideoResult[] = [];

  // soundcloud api: search audio playlist by keyword
  // current api is not official, so it might be unstable
  // soundcloud client id may expire
  // How to get new client id? -> https://stackoverflow.com/questions/40992480/getting-a-soundcloud-api-client-id
  @action.bound
  async searchAudioCollection(name: string) {
    if (!name) return;
    this.isAudioLoading = true;

    try {
      const res = await fetch('/search/playlists_without_albums?' +
        `q=${name}` +
        `&client_id=${API.SOUND_CLOUD_CLIENT_ID}` +
        '&limit=20' +
        '&offset=0' +
        '&linked_partitioning=1' +
        `&app_version=${API.SOUND_CLOUD_APP_VERSION}` +
        '&app_locale=en');
      const data = await res.json();

      this.isAudioLoading = false;
      this.audioCollections = (data as AudioResponse).collection;
      if (this.videoResults === undefined ||
        this.audioCollections.length === 0
      ) message.error('Track Not Found');
    } catch (err) {
      this.isAudioLoading = false;
      message.error(err.message);
    }
  }

  // youtube api: search video by keywords
  // doc - https://developers.google.com/youtube/v3/docs/search
  @action.bound
  async searchVideo(name: string) {
    if (!name) return;
    this.isVideoLoading = true;

    try {
      const res = await fetch(`${API.YOUTUBE_BASE_API}/search?` +
        `q=${name}` +
        `&key=${API.YOUTUBE_API_KEY}` +
        '&maxResults=20' +
        '&part=snippet' +
        '&type=video'
      );
      const data = await res.json();

      this.isVideoLoading = false;
      this.videoResults = (data as VideoResponse).items;
      if (this.videoResults === undefined ||
        this.videoResults.length === 0
      ) message.error('Video Not Found');
    } catch (err) {
      this.isAudioLoading = false;
      message.error(err.message);
    }
  }

  @action.bound
  clearVideoResults() {
    this.videoResults = [];
  }

  @action.bound
  clearAudioCollection() {
    this.audioCollections = [];
  }
}

// export instance
export const profileStore =
  shouldApplyDemoStore()
    ? new DemoProfileStore() as unknown as ProfileStore
    : new ProfileStore();


// export context
export const ProfileStoreCtx = createContext(profileStore);

// export class
const ProfileStoreClass =
  shouldApplyDemoStore()
    ? DemoProfileStore
    : ProfileStore;
export default ProfileStoreClass;