import React, { useState, useContext } from 'react';
import { withTheme } from 'styled-components';
import { Form, Col, Row, Input } from 'antd';
import {
  UserOutlined,
  LockOutlined
} from '@ant-design/icons';
import { ProfileStoreCtx } from '../../store/profileStore';
import {
  FormWrapper,
  ConfirmBtn,
  CancelBtn,
  SignUpBtn,
  FormDrawer
} from './styled';

function LoginForm({ theme }: { theme: AppTheme }) {
  const [visible, setVisible] = useState(false);
  const [form] = Form.useForm();
  const profileStore = useContext(ProfileStoreCtx);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const onLogin = () => {
    form.validateFields()
      .then(async values => {
        form.resetFields();

        const success = await profileStore.login({
          client: values.client,
          password: values.password
        });

        if (!success) return;
        setVisible(false);
      });
  };

  return (
    <FormWrapper>
      <SignUpBtn onClick={showDrawer}>Login</SignUpBtn>
      <FormDrawer
        title="Welcome Back"
        width={`calc(100% - ${theme.sider.width})`}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div>
            <CancelBtn onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </CancelBtn>
            <ConfirmBtn onClick={onLogin} type="primary">
              Login
            </ConfirmBtn>
          </div>
        }
      >
        <Form
          layout="vertical"
          form={form}
          hideRequiredMark
        >
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="client"
                label={<span><UserOutlined /> Client Name</span>}
                rules={[{ required: true, message: 'Please enter client name' }]}
              >
                <Input placeholder="Enter client name" />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="password"
                label={<span><LockOutlined /> Password</span>}
                rules={[{ required: true, message: 'Please enter your password' }]}
              >
                <Input.Password placeholder="Enter password" />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </FormDrawer>
    </FormWrapper>
  );
}

export default withTheme(LoginForm);
