// DEMO stores are only used for github page demo when there is no running server
// it should be removed if there is a running server for demo
import { observable, action } from 'mobx';
import { message } from 'antd';

export default class DemoPecsStore {
  @observable
  pecsArr: Pecs[] = [];

  @action.bound
  async getPecs() {
    this.pecsArr = [];
  }

  @action.bound
  async addPecs(newPecs: { imageId: string, name: string }) {
    const pecs: Pecs = {
      _id: new Date().toISOString(),
      name: newPecs.name,
      clientId: Math.random() * 100000,
      imageId: 'default'
    };
    message.success('Pecs has been added');
    this.pecsArr.push(pecs);
  }

  @action.bound
  async removePecs(imageId: string) {
    this.pecsArr = [];
  }

  @action.bound
  async updatePecs(imageId: string) {
    this.pecsArr = [];
  }
}