/* eslint-disable import/no-extraneous-dependencies, @typescript-eslint/no-explicit-any */
import React from 'react';
import styled from 'styled-components';
import {
  BulbFilled,
  BulbOutlined,
  AudioOutlined,
  AudioFilled
} from '@ant-design/icons';
import { useToggle } from '../../../hooks/index';
import Guide from './Guide';
import { StartMode } from '../../../assets/const';

const createBtn = (antdIcon: any) => {
  return styled(antdIcon)`
    font-size: 30px;
    margin-top: 10px;
  `;
};

const GuideBtnOutlined = createBtn(BulbOutlined);
const GuideBtnFilled = createBtn(BulbFilled);
const AudioBtnOutlined = createBtn(AudioOutlined);
const AudioBtnFilled = createBtn(AudioFilled);

const Menu = styled.div`
  position: absolute;
  width: 50px;
  top: 0px;
  left: 0px;
`;

interface Props {
  showVoiceCommand: boolean;
  toggleShowVoiceCommand: React.Dispatch<React.SetStateAction<boolean>>;
  startMode: StartMode;
}

function TemperatureMenu({
  showVoiceCommand,
  toggleShowVoiceCommand,
  startMode
}: Props) {
  const [isGuideOpen, toggleGuide] = useToggle(false);

  return (
    <>
      <Guide
        showVoiceCommand={showVoiceCommand}
        isOpen={isGuideOpen}
        toggleGuide={toggleGuide}
        startMode={startMode}
      />
      <Menu>
        {/* Guide Display */}
        {isGuideOpen
          ?
            <GuideBtnFilled
              onClick={toggleGuide}
            />
          :
            <GuideBtnOutlined
              onClick={toggleGuide}
            />}

        {/* Voice Control Display */}
        {showVoiceCommand
          ?
            <AudioBtnFilled
              onClick={toggleShowVoiceCommand}
            />
          :
            <AudioBtnOutlined
              onClick={toggleShowVoiceCommand}
            />}
      </Menu>
    </>
  );
}

export default TemperatureMenu;
