// TODO: validator for user porfiles
// See https://ant.design/components/form/#components-form-demo-register for more about validate antd form

export const passwordValidator = (rule: unknown, value: string) => {
  if (value.length > 32)
    return Promise.reject('Password should be at most is 32 characters');
  if (value.length < 8)
    return Promise.reject('Password should be at least 8 characters');
  if (value.indexOf(' ') >= 0)
    return Promise.reject('Password should not contain space');
  if (!value.match(/.*[0-9]/))
    return Promise.reject('Require that at least one digit');
  if (!value.match(/.*[a-z]/))
    return Promise.reject('Require that at least one lowercase letter');
  if (!value.match(/.*[A-Z]/))
    return Promise.reject('Require that at least one uppercase  letter');
  if (!value.match(/.*[*.!@$%^&]/))
    return Promise.reject('Require that at least one special character: !@$%^&)');
  return Promise.resolve();
};

export const clientNameValidator = (rule: unknown, value: string) => {
  if (value.length > 20)
    return Promise.reject('Client name should be at most 20 characters');
  if (value.length < 3)
    return Promise.reject('Client name should be at least 3 characters');
  if (value.indexOf(' ') >= 0)
    return Promise.reject('Client name should not contain space');
  if (!value.match(/^[a-zA-Z]*$/))
    return Promise.reject('Client name should only contain characters');
  return Promise.resolve();
};

export const pecsNameValidator = (rule: unknown, value: string) => {
  if (value.length > 50)
    return Promise.reject('Pecs name should be at most 50 characters');
  if (value.length < 3)
    return Promise.reject('Pecs name should be at least 3 characters');
  if (!value.match(/^[a-zA-Z\s]*$/))
    return Promise.reject('Pecs name should only contain characters and space');
  return Promise.resolve();
};

