import React, { useContext } from 'react';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { ProfileStoreCtx } from '../../store/profileStore';
import SearchVideo from './searchVideo';
import { WidgetWrapper, IFrame } from '../styled/index';
import { API } from '../../assets/const';

const VideoWidgetWrapper = styled(WidgetWrapper)`
`;

const VideoWidget = observer(() => {
  const profileStore = useContext(ProfileStoreCtx);

  return (
    <VideoWidgetWrapper>
      <SearchVideo />
      <IFrame
        title="youtube"
        scrolling="no"
        frameBorder="no"
        src={
          `${API.YOUTUBE_WIDGET_BASE_API}` +
          `${profileStore.currentVideo?.id}`
        }
      />
    </VideoWidgetWrapper>
  );
});

export default VideoWidget;
