/* eslint-disable @typescript-eslint/no-explicit-any */
import React, {
  useRef,
  useState,
  useEffect,
  useContext
} from 'react';
import { ThemeProvider } from 'styled-components';
import Timer from 'react-compound-timer';
import { observer } from 'mobx-react';
import { AppLayout, AppContent, AppCarousel } from './styled';
import { ProfileStoreCtx } from '../store/profileStore';
import AppHeader from '../components/header/header';
import AppFooter from '../components/footer/footer';
import TemperaturePage from './slidePages/temperaturePage/temperaturePage';
import WidgetPage from './slidePages/widgetPage/widgetPage';
import ProfilePage from './slidePages/profilePage/profilePage';
import { speak } from '../utils';
import { defaultTheme, easyTheme } from '../assets/style/theme';
import { StartMode, ShowerState, CONST } from '../assets/const';

// HOC with timer countdown
const withTimer =
  (WrappedComponent: any) =>
    (wrappedComponentProps: any) => {
      const profileStore = useContext(ProfileStoreCtx);
      const { currentUser } = profileStore;

      // init timer countdown
      const [timerCountdown, setTimerCountdown] = useState(
        (currentUser
          ? currentUser.timer
          : CONST.DEFAULT_TIME) * 60 * 1000);

      const timerProps = {
        initialTime: timerCountdown,
        direction: 'backward' as 'backward' | 'forward' | undefined,
        startImmediately: false,
        formatValue: (value: number) => `${(value < 10 ? `0${value}` : value)}`,
      };
      return (
        <Timer {...timerProps}>
          {(timerRenderProps: TimerRenderProps) =>
            <WrappedComponent
              {...wrappedComponentProps}
              timer={timerRenderProps}
              timerCountdown={timerCountdown}
              setTimerCountdown={setTimerCountdown}
            />}
        </Timer>
      );
    };

// those props come from withTimer HOC above
interface Props {
  timer: TimerRenderProps;
  timerCountdown: number;
  setTimerCountdown: React.Dispatch<React.SetStateAction<StartMode>>;
}

function Layout({
  timer,
  timerCountdown,
  setTimerCountdown
}: Props) {
  const profileStore = useContext(ProfileStoreCtx);
  const { currentUser } = profileStore;
  const currentUserTimer = currentUser?.timer;
  const currentUserVolume = currentUser?.volume;

  // init volume countdown
  const [volumeCountdown, setVolumeCountdown] = useState(currentUser
    ? currentUser.timer
    : CONST.DEFAULT_VOLUME);

  // carousel ref & state
  const carouseEl = useRef(null);
  const [index, setIndex] = useState<number>(1);

  // voluem countdown
  // maybe refactor: use mobx or composed hook
  const [volume, setVolume] = useState(volumeCountdown);
  const [startMode, setStartMode] = useState(StartMode.Time);

  // shower state
  const [showerState, setShowerState] = useState(ShowerState.Inited);

  // display mode -> easy | normal
  const [easyMode, setEasyMode] = useState<boolean>(false);

  // Theme | normal -> easy
  const theme: AppTheme = easyMode ? easyTheme : defaultTheme;

  // reset volume|timer on user change 
  useEffect(() => {
    // only applys when shower is in inited state
    if (showerState !== ShowerState.Inited) return;

    const initalTimeCountdown =
      (currentUserTimer || CONST.DEFAULT_TIME) * 60 * 1000;
    timer.setTime(initalTimeCountdown);
    setTimerCountdown(initalTimeCountdown);

    const initalVolumeCountdown =
      currentUserVolume || CONST.DEFAULT_VOLUME;
    setVolume(initalVolumeCountdown);
    setVolumeCountdown(initalVolumeCountdown);
  }, [
    showerState,
    timer,
    currentUserTimer,
    currentUserVolume,
    setTimerCountdown
  ]);

  // react to countdown and end shower
  useEffect(() => {
    // only applys when shower is in started state
    if (showerState !== ShowerState.Started) return;

    // on time change
    if (startMode === StartMode.Time) {
      if (timer.getTime() <= 0) {
        setShowerState(ShowerState.End);
        speak('Shower Ended');
      }

      // voice feedback - low time
      if (Math.abs(timer.getTime() - 1000 * 60 * CONST.LOW_TIME) < 500)
        speak(`${CONST.LOW_TIME} seconds left`);
    }

    // on volume change
    if (startMode === StartMode.Volume) {
      if (volume <= 0) {
        setShowerState(ShowerState.End);
        speak('Shower Ended');
      }

      // voice feedback - low volume
      if (volume === CONST.LOW_LITRE)
        speak(`${CONST.LOW_LITRE} litres left`);
    }
  }, [volume, startMode, timer, showerState]);

  return (
    <ThemeProvider theme={theme}>
      <AppLayout>
        <AppHeader
          easy={easyMode}
          setMode={setEasyMode}
          startMode={startMode}
          timer={timer}
          timerCountdown={timerCountdown}
          setVolume={setVolume}
          volume={volume}
          volumeCountdown={volumeCountdown}
          showerState={showerState}
          setShowerState={setShowerState}
          finished={showerState === ShowerState.End}
        />
        <AppContent>
          <AppCarousel
            infinite={false}
            dotPosition='bottom'
            ref={carouseEl}
            afterChange={(current: number) => {
              setIndex(current);
            }}
          >
            <TemperaturePage
              easy={easyMode}
              startMode={startMode}
              setStartMode={setStartMode}
              timer={timer}
              timerCountdown={timerCountdown}
              setVolume={setVolume}
              volume={volume}
              volumeCountdown={volumeCountdown}
              showerState={showerState}
              setShowerState={setShowerState}
              finished={showerState === ShowerState.End}
              setTimerCountdown={setTimerCountdown}
              setVolumeCountdown={setVolumeCountdown}
            />
            <WidgetPage easy={easyMode} />
            <ProfilePage />
          </AppCarousel>
        </AppContent>
        <AppFooter
          prev={() => {
            (carouseEl as React.MutableRefObject<any>).current.prev();
          }}
          next={() => {
            (carouseEl as React.MutableRefObject<any>).current.next();
          }}
          index={index}
        />
      </AppLayout>
    </ThemeProvider>
  );
}

export default observer(withTimer(observer(Layout)));
