import React, { useContext, useCallback } from 'react';
import styled from 'styled-components';
import { Input } from 'antd';
import { observer } from 'mobx-react';
import { ProfileStoreCtx } from '../../store/profileStore';
import { WidgetSearchCancelBtn } from '../styled/index';
import { placeholder } from '../../assets/img/index';

const StyledSearch = styled(Input.Search)`
  position: relative;
  width: 100%;
  height: ${props => props.addonBefore ? '60px' : '40px'};
  .ant-input-group-addon {
    font-size: 25px;
    font-weight: bold;
    color: gray;
    width: 100px;
    height: inherit;
    padding: 0;
    border: none;
  }
  .ant-input {
    font-size: 25px;
  }
  .ant-input-wrapper{
    height: inherit;
  }
`;

const PlayListWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: auto;
  height: calc(100% - 40px);
`;

const PlayListTitle = styled.h1`
  color: white;
  margin: 0;
`;

const CollectionWrapper = styled.div`
  position: relative;
  margin: 5px;
  height: 100px;
  width: 100px;
  overflow: hidden;
  img {
    margin:0;
    width: 100%;
    height: 100%;
  }
  cursor: pointer;
`;

interface ResultWrapperProp {
  fullSize: boolean
}

const ResultWrapper = styled.div<ResultWrapperProp>`
  position: absolute;
  width: 100%;
  z-index: 1;
  background: rgba(0,0,0,0.4);
  top: ${props => props.fullSize ? '60px' : '40px'};
`;

interface Props {
  addonBefore?: React.ReactNode | undefined | string;
}

const SearchMusic = observer(({ addonBefore }: Props) => {
  const profileStore = useContext(ProfileStoreCtx);

  const renderResult = useCallback((audioCollections: AudioCollection[]) => {
    if (audioCollections.length <= 0) return null;

    return (
      <ResultWrapper fullSize={!!addonBefore}>
        <PlayListTitle> Select Your Track
          <WidgetSearchCancelBtn onClick={profileStore.clearAudioCollection}>
            X
          </WidgetSearchCancelBtn>
        </PlayListTitle>
        <PlayListWrapper>
          {profileStore.audioCollections.map(
            (audio: AudioCollection) => {
              return (
                <CollectionWrapper
                  key={audio.id}
                  onClick={e => {
                    e.stopPropagation();
                    profileStore.setAudioCollection(
                      audio.id,
                      audio.tracks[0]?.artwork_url
                    );
                  }}
                >
                  <img
                    src={audio.tracks[0]?.artwork_url
                      ? audio.tracks[0]?.artwork_url
                      : placeholder.soundtrack}
                    alt='artwork'
                  />
                </CollectionWrapper>
              );
            }
          )}
        </PlayListWrapper>
      </ResultWrapper>
    );
  }, [profileStore, addonBefore]);

  return (
    <>
      <StyledSearch
        onSearch={(value: string) => {
          profileStore.searchAudioCollection(value);
        }}
        addonBefore={addonBefore}
        disabled={profileStore.isAudioLoading}
        placeholder='Search Audio Track'
      />
      {renderResult(profileStore.audioCollections)}
    </>
  );
});

export default SearchMusic;
