/* eslint-disable no-underscore-dangle */
import React, { useState, useContext, useEffect } from 'react';
import { withTheme } from 'styled-components';
import { Form, Col, Row, Input, DatePicker, InputNumber } from 'antd';
import moment from 'moment';
import {
  IdcardOutlined,
  CameraOutlined,
  CalendarOutlined,
  BgColorsOutlined,
  FundProjectionScreenOutlined,
  FireOutlined,
  MedicineBoxFilled,
  ClockCircleOutlined,
  DashboardOutlined
} from '@ant-design/icons';
import AvatarPicker from '../avatarPicker/avatarPicker';
import TemperaturePicker from '../temperaturePicker/temperaturePicker';
import { CONST } from '../../assets/const';
import {
  FormWrapper,
  ConfirmBtn,
  CancelBtn,
  AddUserBtn,
  FormDrawer
} from './styled';
import { ProfileStoreCtx } from '../../store/profileStore';

const fields = {
  name: {
    type: 'string',
    label: <span><IdcardOutlined /> User Name</span>,
    message: 'Please enter user name',
    initialValue: undefined
  },
  iconId: {
    type: 'icon',
    label: <span><CameraOutlined /> Choose Your Icon</span>,
    message: 'Please select an icon',
    initialValue: 1
  },
  dob: {
    type: 'date',
    label: <span><CalendarOutlined /> Birthday</span>,
    message: 'Please enter user birthday',
    initialValue: undefined
  },
  fColor: {
    type: 'string',
    label: <span><BgColorsOutlined /> Favourite Color</span>,
    message: 'Please enter favourite color',
    initialValue: undefined
  },
  fVideo: {
    type: 'string',
    label: <span><FundProjectionScreenOutlined /> Favourite Show</span>,
    message: 'Please enter favourite show',
    initialValue: undefined
  },
  fTemperature: {
    type: 'number',
    label: <span><FireOutlined /> Favourite Temperature</span>,
    message: 'Please enter favourite temperature',
    min: CONST.MIN_TEMP,
    max: CONST.MAX_TEMP,
    formatter: (value: string) => `${value}`,
    parser: (value: string) => value.replace('', ''),
    initialValue: CONST.MIN_TEMP
  },
  temperatureRange: {
    type: 'range',
    label: <span><FireOutlined /> Temperature Range</span>,
    message: 'Please enter temperature range',
    initialValue: [CONST.MIN_TEMP, CONST.MAX_TEMP]
  },
  timer: {
    type: 'number',
    label: <span><ClockCircleOutlined /> Timer </span>,
    message: 'Please enter shower time limit',
    min: CONST.MIN_TIMER,
    max: CONST.MAX_TIMER,
    formatter: (value: string) => `${value}min`,
    parser: (value: string) => value.replace('min', ''),
    initialValue: CONST.MIN_TIMER
  },
  volume: {
    type: 'number',
    label: <span><DashboardOutlined /> Volume </span>,
    message: 'Please enter shower volume limit',
    min: CONST.MIN_LITRE,
    max: CONST.MAX_LITRE,
    formatter: (value: string) => `${value}L`,
    parser: (value: string) => value.replace('L', ''),
    initialValue: CONST.MIN_TIMER
  },
  medicalNotes: {
    type: 'string',
    label: <span><MedicineBoxFilled /> Medical Notes</span>,
    message: 'Please medical notes',
    initialValue: undefined
  }
};

interface Props {
  editingProfile: null | UserProfile;
  onEndEditing: () => void;
  theme: AppTheme;
}

function UserProfileForm({ theme, editingProfile, onEndEditing }: Props) {
  const [visible, setVisible] = useState(false);
  const [form] = Form.useForm();
  const profileStore = useContext(ProfileStoreCtx);

  useEffect(() => {
    if (editingProfile) {
      form.setFieldsValue({
        ...editingProfile,
        dob: moment(editingProfile.dob, 'MMM Do YYYY')
      });
      setVisible(true);
    } else {
      form.resetFields();
    }
  }, [editingProfile, form]);


  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    onEndEditing();
    setVisible(false);
  };

  const onEdit = () => {
    form.validateFields()
      .then(values => {
        form.resetFields();

        const userProfile: UserProfile = {
          name: values.name,
          dob: values.dob.format('MMM Do YYYY'),
          _id: (editingProfile as UserProfile)._id,
          clientId: (editingProfile as UserProfile).clientId,
          iconId: values.iconId,
          fColor: values.fColor,
          fVideo: values.fVideo,
          fTemperature: values.fTemperature,
          temperatureRange: values.temperatureRange,
          timer: values.timer,
          volume: values.volume,
          averageShowerTime: (editingProfile as UserProfile).averageShowerTime,
          medicalNotes: values.medicalNotes
        };

        profileStore.updateUserProfile(userProfile);
        onClose();
      });
  };

  const onAdd = () => {
    form.validateFields()
      .then(values => {
        form.resetFields();

        const userProfile: UserProfile = {
          name: values.name,
          dob: values.dob.format('MMM Do YYYY'),
          _id: undefined,
          clientId: (profileStore.clientProfile as ClientProfile)._id,
          iconId: values.iconId,
          fColor: values.fColor,
          fVideo: values.fVideo,
          fTemperature: values.fTemperature,
          temperatureRange: values.temperatureRange,
          timer: values.timer,
          volume: values.volume,
          averageShowerTime: 0,
          medicalNotes: values.medicalNotes
        };

        profileStore.addUserProfile(userProfile);
        onClose();
      });
  };

  // this any should be specified later
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const renderFormItem = (options: any): React.ReactNode | null => {
    switch (options.type) {
      case 'string':
        return <Input placeholder={options.message as string} />;
      case 'date':
        return <DatePicker />;
      case 'icon':
        return (
          <AvatarPicker imageId={
            editingProfile
              ? editingProfile.iconId
              : undefined
          }
          />
        );
      case 'number':
        return <InputNumber
          min={options.min}
          max={options.max}
          defaultValue={options.value}
          value={options.value}
          formatter={options.formatter}
          parser={options.parser}
        />;
      case 'range':
        return <TemperaturePicker value={options.value} />;
      default:
        return null;
    }
  };

  return (
    <FormWrapper>
      <AddUserBtn onClick={showDrawer} />
      <FormDrawer
        title="Add New User Profile"
        width={`calc(100% - ${theme.sider.width})`}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div>
            <CancelBtn onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </CancelBtn>
            <ConfirmBtn onClick={editingProfile ? onEdit : onAdd} type="primary" htmlType="submit">
              {editingProfile ? 'Edit Profile' : 'Add Profile'}
            </ConfirmBtn>
          </div>
        }
        forceRender
      >
        <Form
          layout="vertical"
          hideRequiredMark
          form={form}
        >
          {Object.entries(fields).map(([key, options]) => {
            return (
              <Row gutter={16} key={key}>
                <Col span={24}>
                  <Form.Item
                    name={key}
                    label={options.label}
                    rules={[{ required: true, message: options.message }]}
                    initialValue={options.initialValue}
                  >
                    {renderFormItem(options)}
                  </Form.Item>
                </Col>
              </Row>
            );
          })}
        </Form>
      </FormDrawer>
    </FormWrapper>
  );
}

export default withTheme(UserProfileForm);
