/* eslint-disable no-underscore-dangle */
import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { Card, Descriptions } from 'antd';
import {
  EditOutlined,
  DeleteOutlined,
  IdcardOutlined,
  CalendarOutlined,
  BgColorsOutlined,
  FundProjectionScreenOutlined,
  FireOutlined,
  MedicineBoxFilled,
  ClockCircleOutlined,
  DashboardOutlined
} from '@ant-design/icons';
import { UserProfileForm } from '../../../../components/forms';
import { ProfileStoreCtx } from '../../../../store/profileStore';
import { avatars } from '../../../../assets/img/index';
import { composeIconUrl } from '../../../../utils';

const { Meta } = Card;

const UserProfileWrapper = styled.div`
  display: flex;
  width:92%;
  overflow-x: auto;
  padding-left: 20px;
  &::-webkit-scrollbar {
    -webkit-appearance: none;
    margin-left: 20px;
  }

  &::-webkit-scrollbar:horizontal {
    height: 8px;
    margin-left: 20px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 8px;
    border: 2px solid white;
    background-color: rgba(0, 0, 0, .5);
    margin-left: 20px;
  }
  &::-webkit-scrollbar-track { 
    border-radius: 8px; 
    margin-left: 20px;
  }
`;

const UserDetailWrapper = styled.div`
  margin: 20px;
  box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
`;

const UserProfileDescription = styled(Descriptions)`
  padding: 0px;
  .ant-descriptions-item-content{
    padding: 5px 5px;
    font-size: ${props => `calc(${props.theme.font.sm}*0.8)`};
  }
  .ant-descriptions-item-label{
    padding: 5px 5px;
    background: lightgray;
    font-weight: 500;
    span {
      font-size: ${props => `calc(${props.theme.font.sm}*0.6)`};
    }
  }
`;

const CardWrapper = styled.div`
  border: none;
  width: 150px;
  min-width: 150px;
  .ant-card-body{
    padding: 5px;
  }
  box-shadow: ${props => props.theme.isSelected ? '5px 5px 5px rgba(0, 0, 0, 0.2)' : ''};
  opacity: ${props => props.theme.isSelected ? 1 : 0.5};
  margin: 20px;
  overflow: hidden;
`;

const UserProfileSection = () => {
  const profileStore = useContext(ProfileStoreCtx);
  const [editingProfile, setEditing] = useState<null | UserProfile>(null);

  const renderUsersProfile = (usersProfile: UserProfile[]) => {
    return (
      <>
        <UserProfileForm
          editingProfile={editingProfile}
          onEndEditing={() => setEditing(null)}
        />
        <UserProfileWrapper
          onTouchMove={e => e.stopPropagation()}
        >
          {usersProfile.map((userProfile: UserProfile) => {
            return (
              <CardWrapper
                key={userProfile._id}
                theme={{
                  isSelected:
                    profileStore.selectedUserProfile?._id
                    === userProfile._id
                }}
              >
                <Card
                  onClick={e => {
                    profileStore.setSelectedUserProfile(userProfile);
                  }}
                  cover={
                    <img
                      src={avatars[userProfile.iconId]
                        || composeIconUrl(userProfile.iconId)}
                      style={{ height: 152, width: 152 }}
                      alt="avatar"
                    />
                  }
                  actions={[
                    <EditOutlined
                      key="edit"
                      onClick={() => setEditing(userProfile)}
                    />,
                    <DeleteOutlined
                      key="ellipsis"
                      onClick={(e) => {
                        profileStore.removeUserProfile(
                          userProfile._id as number
                        );
                        e.stopPropagation();
                      }}
                    />,
                  ]}
                >
                  <Meta
                    title={userProfile.name}
                  />
                </Card>
              </CardWrapper>
            );
          })}

        </UserProfileWrapper>
        {profileStore.selectedUserProfile ?
          <UserDetailWrapper>
            <UserProfileDescription
              bordered
              layout="vertical"
              column={4}
            >
              <Descriptions.Item
                label={
                  <span>
                    <IdcardOutlined />
                    {` ${profileStore.selectedUserProfile.name}`}
                  </span>
                }
              >
                <img
                  src={
                    avatars[profileStore.selectedUserProfile.iconId]
                    || composeIconUrl(profileStore.selectedUserProfile.iconId)
                  }
                  style={{ height: 50, width: 50 }}
                  alt="avatar"
                />
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><CalendarOutlined /> Birthday</span>}
              >
                {profileStore.selectedUserProfile.dob}
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><BgColorsOutlined /> Color</span>}
              >
                {profileStore.selectedUserProfile.fColor}
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><FundProjectionScreenOutlined /> Show</span>}
              >
                {profileStore.selectedUserProfile.fVideo}
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><FireOutlined /> Favourite Temperature</span>}
              >
                {profileStore.selectedUserProfile.fTemperature} 
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><ClockCircleOutlined /> Average Shower Time</span>}
              >
                {profileStore.selectedUserProfile.averageShowerTime} mins
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><FireOutlined /> Temperature Lock</span>}
              >
                {
                  `${profileStore.selectedUserProfile.temperatureRange[0]}~
                  ${profileStore.selectedUserProfile.temperatureRange[1]} `
                }
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><ClockCircleOutlined /> Timer</span>}
              >
                {profileStore.selectedUserProfile.timer} mins
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><DashboardOutlined /> Volume</span>}
              >
                {profileStore.selectedUserProfile.volume} liters
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><MedicineBoxFilled /> Medical Notes</span>}
                span={3}
              >
                {profileStore.selectedUserProfile.medicalNotes}
              </Descriptions.Item>
            </UserProfileDescription>
          </UserDetailWrapper>
          :
          null}
      </>
    );
  };

  return (
    profileStore.isLogin
      ? renderUsersProfile(
        profileStore.clientProfile?.users as UserProfile[]
      )
      : null
  );
};

export default observer(UserProfileSection);
