/* eslint-disable no-underscore-dangle */
import http from './http';

export default class ClientService {
  static api = 'clients';

  static resetPasswordApi = 'resetpassword';

  static async update(updatedFields: {
    client?: string,
    iconId?: string,
    video?: Video,
    audio?: Audio
  }): Promise<[undefined, ClientProfile] | [MyError, undefined]> {
    const request = {
      url: `${ClientService.api}`,
      options: {
        method: 'PUT',
        body: JSON.stringify(updatedFields)
      },
      requireAuth: true
    };
    try {
      const res = await http(request);
      const data: ClientProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async resetPassword(msg: { password: string })
    : Promise<[undefined, Message] | [MyError, undefined]> {
    const request = {
      url: `${ClientService.api}/${ClientService.resetPasswordApi}`,
      options: {
        method: 'POST',
        body: JSON.stringify(msg)
      },
      requireAuth: true
    };
    try {
      const res = await http(request);
      const data: Message = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }
}
