/* eslint-disable react/require-default-props */
import React from 'react';
import styled from 'styled-components';
import { EditOutlined, DeleteOutlined } from '@ant-design/icons';

const SectionWrapper = styled.div`
  display: flex;
  flex-direction: ${(props: SectionProps) => props.flexDirection};
  margin-top: 2px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 24px;
  background: white;
  box-shadow: 0px 2px 2px 2px rgba(0,0,0,0.2);
  position: relative;
  width: ${(props: SectionProps) => `${props.width}%`}
`;

const OptionWrapper = styled.div`
  position: absolute;
  width: 10px;
  right: 30px;
  top: -3px;
`;

const EditBtn = styled(EditOutlined)`
  font-size: 25px;
  margin: 5px;
`;

const DeleteBtn = styled(DeleteOutlined)`
  font-size: 25px;
  margin: 5px;
`;

interface SectionProps {
  children: React.ReactNode;
  flexDirection: 'column' | 'row';
  onEdit?: (() => void) | undefined;
  onDelete?: (() => void) | undefined;
  width?: number;
}

function Section({
  children,
  flexDirection = 'column',
  onEdit = undefined,
  onDelete = undefined,
  width = undefined
}: SectionProps) {
  return (
    <SectionWrapper width={width} flexDirection={flexDirection}>
      {children}
      <OptionWrapper>
        {onEdit ? <EditBtn key="edit" onClick={onEdit} /> : null}
        {onDelete ? <DeleteBtn key="delete" onClick={onDelete} /> : null}
      </OptionWrapper>
    </SectionWrapper>
  );
}

export default Section;
