import React, { useContext, useCallback } from 'react';
import styled from 'styled-components';
import ReactFlow, { Elements } from 'react-flow-renderer';
import { Moment } from 'moment';
import { observer } from 'mobx-react';
import { ToDoStoreCtx, FORMAT } from '../../store/toDoStore';
import { ToDoListItem } from './toDoList';

const ToDoFlowWrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 80%;
  background: rgba(255,255,255,0.5);
  .react-flow__node {
    background: rgba(0,0,0,0.1);
    width: unset;
  }
`;

const ToDoFlow = ({ date }: { date: Moment }) => {
  const toDoStore = useContext(ToDoStoreCtx);
  const createElements = useCallback((): Elements => {
    const dateString = date.format(FORMAT);
    const elements: Elements = [];
    const x = 20;
    let y = 20;
    let lastId = null;
    const step = 100;
    
    // return empity list if no todo for current data
    if (!toDoStore.toDoList ||
      !toDoStore.toDoList[dateString]
    ) return elements;

    // get todo list for current date
    for (const todo of toDoStore.toDoList[dateString]) {
      const currentId = todo.id.toString();
      elements.push({
        id: todo.id.toString(),
        data: {
          label: <ToDoListItem key={todo.id} date={date} id={todo.id} />
        },
        position: { x, y }
      });

      // shift next todo to the bottom
      y += step;

      // link previous todo to current todo
      if (lastId) {
        elements.push({
          id: `e${lastId}${currentId}`,
          source: lastId,
          target: currentId,
          animated: true
        });
      }
      lastId = currentId;
    }
    return elements;
  }, [date, toDoStore.toDoList]);

  return (
    <ToDoFlowWrapper>
      <ReactFlow elements={createElements()} />
    </ToDoFlowWrapper>
  );
};

export default observer(ToDoFlow);