/* eslint-disable @typescript-eslint/no-explicit-any, react/require-default-props */
import React, { useState, useRef, useEffect } from 'react';
import { Radio } from 'antd';
import styled from 'styled-components';
import ImageUpload from '../imageUpload';
import { avatars } from '../../assets/img';
import { isDefaultImage, isElementInViewport } from '../../utils';

const AvatarPickerWrapper = styled.div`
  width: 100%;
  .ant-radio-button-wrapper {
    border: 0px;
    
  }
  .ant-radio-button-wrapper:not(:first-child)::before {
    background-color: unset;
  }
`;

const RadioImg = styled.img`
  height: 100%;
  width: 100%;
  opacity: 0.5;
  transition: all 0.2s linear;
`;

const RadioImgSelected = styled(RadioImg)`
  opacity: 1;
`;

const RadioButton = styled(Radio.Button)`
  height: 0;
  padding-bottom: 14%;
  width: 14%;
  margin: 1%;
  span {
    position: absolute;
    top: 0;
    bottom: 0 !important;
    left: 0;
    right: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

interface Props {
  isSignUp?: boolean;
  [k: string]: any;
}

// props are injected from Antd Form
function AvatarPicker({ isSignUp, imageId, handleChange, ...props }: Props) {
  const [selectedValue, setSelectedValue] = useState<string>(imageId);
  const [uploadedImage, setUploadedImage] = useState<string>();

  useEffect(() => {
    setUploadedImage(
      (imageId && !isDefaultImage(imageId))
        ? imageId
        : undefined
    );
  }, [imageId]);

  // this is a hack to select radio button once image has been uploaded
  // make a div between upload and radio component
  // bubble up click event to the radio
  const radio = useRef<HTMLDivElement>(null);
  useEffect(() => {
    if (radio.current &&
      uploadedImage &&
      isElementInViewport(radio.current)
    ) radio.current.click();
  }, [uploadedImage]);

  const onClick = (e: React.MouseEvent<HTMLInputElement>) => {
    const currentValue = e.currentTarget.value;
    if (handleChange) handleChange(currentValue);
    setSelectedValue(currentValue);
  };

  const onClickUpload = (e: React.MouseEvent<HTMLInputElement>) => {
    const currentValue = e.currentTarget.value;
    setSelectedValue(currentValue);
  };

  // show all 12 icons if it is on sign up form
  // replace last radio with upload if no on sign up form
  const avatarEntries = Object.entries(avatars);
  if (!isSignUp) avatarEntries.pop();

  return (
    <AvatarPickerWrapper>
      <Radio.Group
        className="avatar-picker-radio-group"
        {...props}
      >
        {avatarEntries.map(([i, avatar]) => {
          return (
            <RadioButton
              key={avatar}
              value={i}
              onClick={onClick}
            >
              {selectedValue === i
                ? <RadioImgSelected src={avatar} />
                : <RadioImg src={avatar} />}
            </RadioButton>
          );
        })}

        {!isSignUp &&
          <RadioButton
            key='upload'
            value={uploadedImage}
            onClick={onClickUpload}
            checked={selectedValue === uploadedImage}
          >
            <div ref={radio}>
              <ImageUpload
                imageId={uploadedImage}
                name="image"
                onUploaded={(updatedImageId: string, callback: () => void) => {
                  setSelectedValue(updatedImageId);
                  setUploadedImage(updatedImageId);
                  callback();
                  if (handleChange) handleChange(updatedImageId);
                }}
              />
            </div>
          </RadioButton>}
      </Radio.Group>

    </AvatarPickerWrapper>
  );
}

export default AvatarPicker;
