/* eslint-disable no-console */
import leven from 'leven';
import { API } from '../assets/const';

/**
 * Format Date
 * 
 * @param d Date object
 * @return 'mm/dd/yyyy'
 */
const formatDate = (d: Date): string => {
  let month = `${d.getMonth() + 1}`;
  let day = `${d.getDate()}`;
  const year = d.getFullYear();

  if (month.length < 2)
    month = `0${month}`;
  if (day.length < 2)
    day = `0${day}`;

  return [month, day, year].join('/');
};

/**
 * Rank String by leven distance
 * 
 * @param target: string
 * @param dict: Pecs[]
 * @return Pecs[]
 */
const fuzzyStringRank = (target: string, dict: Pecs[]): Pecs[] => {
  return dict.sort((s1, s2) => leven(s1.name, target) - leven(s2.name, target));
};

/**
 * Speak the string message
 * checkout - https://developers.google.com/web/updates/2014/01/Web-apps-that-talk-Introduction-to-the-Speech-Synthesis-API
 *
 * @param message: string
 * @return void
 */
const speak = (message: string): void => {
  // check if speech api is supported
  if (!SpeechSynthesisUtterance) {
    console.error('SpeechSynthesis api is not sported in your browser');
    return;
  }

  // init speech instance & voice
  const msg = new SpeechSynthesisUtterance(message);
  [msg.voice] = speechSynthesis.getVoices()
    .filter(voice =>
      voice.name === 'Google UK English Female'
    ); // voice loading is async, it may be empty and fallback to default voice

  // speak
  if (window && window.speechSynthesis) window.speechSynthesis.speak(msg);
};

/**
 * Compose url for img element
 *
 * @param imageId: string
 * @return composedUrl: string
 */
const composeIconUrl = (imageId: string): string => {
  return `${API.BASE_API}image/${imageId}`;
};

/**
 * Check if image is not default
 *
 * @param imageId: string
 * @return boolean
 */
const isDefaultImage = (imageId: string) => {
  return imageId.length <= 2;
};

/**
 * Check if element is inside current viewport
 *
 * @param el: HTMLElement
 * @return boolean
 */
const isElementInViewport = (el: HTMLElement) => {
  const rect = el.getBoundingClientRect();

  return (
    rect.top >= 0 &&
    rect.left >= 0 &&
    rect.bottom <= (
      window.innerHeight
      || document.documentElement.clientHeight
    ) && /* or $(window).height() */
    rect.right <= (window.innerWidth || document.documentElement.clientWidth) /* or $(window).width() */
  );
};

/**
 * use demo stores if REACT_APP_SECRET_CODE is 'demo'
 *
 * config REACT_APP_SECRET_CODE in .evn.*** files
 *
 * DEMO stores are only used for github page demo when there is no running server
 * it should be removed if there is a running server for demo
 * @return boolean
 */
const shouldApplyDemoStore = (): boolean => {
  // development and production environment
  return process.env.REACT_APP_SECRET_CODE === 'demo';
};

export {
  formatDate,
  fuzzyStringRank,
  speak,
  composeIconUrl,
  isDefaultImage,
  isElementInViewport,
  shouldApplyDemoStore
};