import { observable, action } from 'mobx';
import { createContext } from 'react';
import moment from 'moment';

export const FORMAT = 'MMM Do YYYY';

const toDoList: ToDoList = {
  [`${moment(new Date()).format(FORMAT)}`]: [
    { id: 1, text: 'todo1', pec: undefined },
    { id: 2, text: 'todo2', pec: undefined },
    { id: 3, text: 'todo3', pec: undefined },
    { id: 4, text: 'todo4', pec: undefined }
  ]
};

class ToDoStore {
  @observable
  toDoList: ToDoList | null = toDoList;

  @action.bound
  addToDo(date: string, text: string, pec: string | undefined) {
    if (!(this.toDoList as ToDoList)[date])
      (this.toDoList as ToDoList)[date] = [];
    (this.toDoList as ToDoList)[date].push(
      { id: Math.floor(Math.random() * 10000), text, pec }
    );
  }

  @action.bound
  async removeToDo(date: string, id: number) {
    (this.toDoList as ToDoList)[date]
      = (this.toDoList as ToDoList)[date].filter(todo => todo.id !== id);
    if ((this.toDoList as ToDoList)[date].length === 0) {
      delete (this.toDoList as ToDoList)[date];
    }
  }
}

export const ToDoStoreCtx = createContext(new ToDoStore());