import React, { useMemo, useEffect } from 'react';
import styled from 'styled-components';
import { message } from 'antd';
import { debounce } from 'lodash';
import {
  AudioOutlined,
  AudioMutedOutlined
} from '@ant-design/icons';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import { ShowerState, StartMode, CONST } from '../../assets/const';

const Wrapper = styled.div`
  > span {
    font-size: ${props => `calc(${props.theme.font.sm}*0.9)`};
    color: gray;
  }
  position: relative;
  button {
    margin: 20px;
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: gray;
    padding: ${props => `calc(${props.theme.font.sm}*0.7)`};
    border-radius: 5px;
    display: inline-block;
    border: none;
    transition: all 0.4s ease 0s;
    &:focus {
      outline: 0;
    }
  }

  .anticon {
    font-size: ${props => `calc(${props.theme.font.sm}*0.9)`};
  }

  .active {
    background: #434343;
    letter-spacing: 1px;
    box-shadow: 5px 40px -10px rgba(0, 0, 0, 0.57);
    transition: all 0.4s ease 0s;
    animation: breathing 5s linear infinite;
  }

  @keyframes breathing {
    0% {
      box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.27);
    }

    50% {
      box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.27);
    }

    100% {
      box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.27);
    }
  }
`;

const CommandWrapper = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
`;

const DD = styled.dd`
  margin-left: 5%;
`;

const Transcript = styled.p`
  display: inline-block;
  margin-top: 5px;
  font-size: ${props => `calc(${props.theme.font.md}*0.9)`};
  position: absolute;
  width: 40vw;
  text-align: left;
  top: 20px;
  color: rgba(0, 0, 0, 0.67);
`;

const ErrorMessage = (
  <>
    Invalid Voice Command
    <CommandWrapper>
      <hr />
      <dl>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'increase by x'
        </dt>
        <DD> - increase xC</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'decrease by x'
        </dt>
        <DD> - decrease xC</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'hotter'
        </dt>
        <DD> - increase 1C</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'cooler' or 'colder'
        </dt>
        <DD> - decrease 1C</DD>
        <dt>
          <span role="img" aria-label="speaker"></span>
          'stop'
        </dt>
        <DD> - stop voice detection</DD>
      </dl>
    </CommandWrapper>
  </>
);

interface VoiceControlBtnProps {
  temperature: number;
  setTemperature: (temperature: number) => void;
  easy: boolean;
  maxTemp: number;
  minTemp: number;
  volume: number;
  setVolume: React.Dispatch<React.SetStateAction<number>>;
  showerState: ShowerState;
  startMode: StartMode;
  timer: TimerRenderProps;
  setTimerCountdown: React.Dispatch<React.SetStateAction<number>>;
  setVolumeCountdown: React.Dispatch<React.SetStateAction<number>>;
}

function VoiceControlBtn({
  setTemperature,
  temperature,
  easy,
  maxTemp,
  minTemp,
  setVolume,
  volume,
  showerState,
  startMode,
  timer,
  setTimerCountdown,
  setVolumeCountdown,
}: VoiceControlBtnProps) {
  const commands = [
    {
      command: 'increase by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.min(maxTemp, temperature + t));
      }
    },
    {
      command: 'decrease by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.max(minTemp, temperature - t));
      }
    },
    {
      command: 'cooler',
      callback: () => {
        setTemperature(Math.max(minTemp, temperature - 1));
      },
      isFuzzyMatch: true
    },
    {
      command: 'colder',
      callback: () => {
        setTemperature(Math.max(minTemp, temperature - 1));
      },
      isFuzzyMatch: true
    },
    {
      command: 'hotter',
      callback: () => {
        setTemperature(Math.min(maxTemp, temperature + 1));
      },
      isFuzzyMatch: true
    },
    {
      command: 'extend',
      callback: () => {
        if (showerState === ShowerState.Started) {
          switch (startMode) {
            case StartMode.Time:
              timer.setTime(timer.getTime() + CONST.TIME_STEP * 60 * 1000);
              setTimerCountdown(current =>
                current + CONST.TIME_STEP * 60 * 1000);
              break;
            case StartMode.Volume:
              setVolume(volume + CONST.VOLUME_STEP);
              setVolumeCountdown(current => current + CONST.VOLUME_STEP);
              break;
            default:
              
          }
        }
      },
      isFuzzyMatch: true
    },
    {
      command: 'stop',
      callback: () => {
        resetTranscript();
        SpeechRecognition.stopListening();
      }
    }
  ];

  const {
    listening,
    transcript,
    resetTranscript
  } = useSpeechRecognition({ commands });

  // clearup transcripts
  const clearTranscript = useMemo(() => debounce((ts: string) => {
    const validation
      = /^(extend|increase|decrease|cooler|colder|hotter|stop)/g;
    if (listening && ts && !ts.match(validation))
      message.error(ErrorMessage, 3);
    if (listening) resetTranscript();
  }, 2000), [resetTranscript, listening]);

  useEffect(() => {
    return clearTranscript(transcript);
  }, [transcript, clearTranscript]);

  // check if browser supports speech api
  if (!SpeechRecognition.browserSupportsSpeechRecognition()) {
    return null;
  }

  return (
    <Wrapper className='voice-btn-wapper'>
      <span>{easy ? 'Voice Command ' : ''}</span>
      {!listening
        ? (
          <button
            type='button'
            onClick={(): void => {
              resetTranscript();
              SpeechRecognition.startListening({ continuous: true });
            }}
          >
            <AudioMutedOutlined />
          </button>
        )
        : (
          <>
            <button
              type='button'
              className='active'
              onClick={SpeechRecognition.stopListening}
            >
              <AudioOutlined />
            </button>
            <Transcript>{transcript}</Transcript>
          </>
        )}
    </Wrapper>
  );
}

export default VoiceControlBtn;
