/* eslint-disable no-underscore-dangle */
import { observable, action } from 'mobx';
import { createContext } from 'react';
import { message } from 'antd';
import { shouldApplyDemoStore } from '../utils';
import { profileStore } from './profileStore';
import DemoPecsStore from './pecsStore.demo';
import PecsService from '../services/Pecs';

class PecsStore {
  @observable
  pecsArr: Pecs[] = [];

  @action.bound
  async getPecs() {
    const [err, pecses] = await PecsService.get();
    if (!pecses || err) {
      if (err?.code === 401) profileStore.logout();
      message.error(err ? err.error : 'Not Authorized');
    } else {
      this.pecsArr = pecses;
    }
  }

  @action.bound
  async addPecs(newPecs: { imageId: string, name: string }) {
    const [err, pecs] = await PecsService.add(newPecs);
    if (!pecs || err) {
      if (err?.code === 401) profileStore.logout();
      message.error(err ? err.error : 'Not Authorized');
    } else {
      this.pecsArr.push(pecs);
      message.success('Pecs has been added');
    }
  }

  @action.bound
  async updatePecs(updatedPecs
  : { id: string, imageId: string, name: string }
  ) {
    const [err, pecs] = await PecsService.update(updatedPecs);
    if (!pecs || err) {
      if (err?.code === 401) profileStore.logout();
      message.error(err ? err.error : 'Not Authorized');
    } else {
      const target
        = this.pecsArr.filter(p => p._id === updatedPecs.id)[0];
      target.imageId = updatedPecs.imageId;
      target.name = updatedPecs.name;
      message.success('Pecs has been updated');
    }
  }

  @action.bound
  async removePecs(pecsToDelete: { id: string, imageId: string }) {
    const [err, pecs] = await PecsService.delete(pecsToDelete);
    if (!pecs || err) {
      if (err?.code === 401) profileStore.logout();
      message.error(err ? err.error : 'Not Authorized');
    } else {
      this.pecsArr = this.pecsArr.filter(p => p._id !== pecsToDelete.id);
      message.success('Pecs has been deleted');
    }
  }
}

export const pecsStore =
  shouldApplyDemoStore()
    ? new DemoPecsStore() as unknown as PecsStore
    : new PecsStore();

export const PecsStoreCtx = createContext(pecsStore);
