/* eslint-disable react/require-default-props */
import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import {
  Modal,
  Input,
  Form,
  message
} from 'antd';
import {
  EditOutlined
} from '@ant-design/icons';
import { PecsStoreCtx } from '../../store/pecsStore';
import useToggle from '../../hooks/useToggle';
import ImageUpload from '../imageUpload';
import { pecsNameValidator } from './validators';

const StyledEditOutlined = styled(EditOutlined)`
  color: white;
  font-size: 30px;
  margin-right: 10px;
  cursor: pointer;
`;

const FormItemLabel = styled.div`
  color: white;
  background: rgb(59,59,59);
  h2 {
    margin: 0px;
  }
`;

const ModalWrapper = styled.div`
  .ant-input {
    font-size: ${props => `calc(${props.theme.font.md}*0.7)`};
    height: 60px;
  }
`;

function UploadPecs({ id = undefined, imageId = undefined, name = undefined }
: { id?: string, imageId?: string, name?: string }) {
  const pecsStore = useContext(PecsStoreCtx);
  const [visible, toggleVisible] = useToggle(false);
  const [uploadedImageId, setUploadedImageId] = useState(imageId);
  const [form] = Form.useForm();

  const handleOk = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    form.validateFields()
      .then(async values => {
        if (!uploadedImageId) {
          message.error('Please upload your pecs');
          return;
        }
        if (id) {
          await pecsStore.updatePecs({
            id,
            imageId: uploadedImageId,
            name: values.name
          });
        } else {
          setUploadedImageId(undefined);
          await pecsStore.addPecs({
            imageId: uploadedImageId,
            name: values.name
          });
          form.resetFields();
        }
        toggleVisible();
      });
  };

  const handleCancel = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    toggleVisible();
  };

  const formName = imageId ? 'Edit Pecs' : 'Edit Pecs';

  return (
    <>
      <StyledEditOutlined onClick={toggleVisible} />
      <Modal
        title={formName}
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <ModalWrapper>
          <Form
            name={formName}
            form={form}
          >
            <FormItemLabel>
              <h2>Upload Pecs</h2>
            </FormItemLabel>
            <ImageUpload
              name='image'
              imageId={uploadedImageId}
              height={400}
              onUploaded={(updatedImgId, callback) => {
                setUploadedImageId(updatedImgId);
                callback();
              }}
            />
            <Form.Item
              name="name"
              rules={[
                {
                  required: true,
                  validator: pecsNameValidator
                },
              ]}
              initialValue={name}
            >
              <Input placeholder="Pecs Name" />
            </Form.Item>
          </Form>
        </ModalWrapper>
      </Modal>
    </>
  );
}

export default UploadPecs;
