
/* eslint-disable react/require-default-props, no-underscore-dangle */
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Upload, message } from 'antd';
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import ImageService from '../../services/Image';
import { composeIconUrl, isDefaultImage } from '../../utils';

interface Container {
  height?: number;
}

const Wrapper = styled.div<Container>`
  width: 100%;
  height: ${props => props.height ? `${props.height}px` : '100%'};
  display: flex;
  justify-content: center;
`;

const StyledUpload = styled(Upload)`
  position: relative;
  width: 100%;
  height: 100%;
  .ant-upload {
    width: 100%;
    height: 100%;
    margin-bottom: 0px;
    img {
      width: 100%;
    }
  }
  .ant-upload.ant-upload-select-picture-card {
    border: none;
    margin-right: 0px;
  }
  .ant-upload.ant-upload-select-picture-card > .ant-upload {
    padding: 0px;
  }
  .image-upload {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .anticon-plus{
    color: rgb(59,59,59);
    font-size: 1.5em;
    position: absolute;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
  }
`;

function getBase64(img: Blob, callback: (url: string) => void) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result as string));
  reader.readAsDataURL(img);
}

function beforeUpload(file: RcFile) {
  const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  if (!isJpgOrPng) {
    message.error('You can only upload JPG/PNG file!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('Image must smaller than 2MB!');
  }
  return isJpgOrPng && isLt2M;
}

function ImageUpload({
  imageId,
  name,
  height,
  onUploaded,
}: {
  imageId?: string,
  name: string,
  height?: number,
  onUploaded: (imageId: string, callback: () => void) => void
}) {
  const [loading, setLoading] = useState<boolean>(false);
  const [imgUrl, setImgUrl] = useState<string | undefined>();

  useEffect(() => {
    if (!imageId || isDefaultImage(imageId)) {
      setImgUrl(undefined);
    } else {
      setImgUrl(composeIconUrl(imageId));
    }
  }, [imageId]);

  const handleChange = async (
    info: UploadChangeParam<UploadFile<File & { _id: string }>>
  ) => {
    if (!info.file.status) return;
    if (info.file.status === 'uploading') {
      setLoading(true);
      return;
    }
    if (info.file.status === 'error') {
      message.error('fail to upload');
      setLoading(true);
      return;
    }
    if (info.file.status === 'done') {
      // if image uploaded, update client icon to new id
      const callback = () => {
        getBase64(info.file.originFileObj as Blob, (imageUrl: string) => {
          setLoading(false);
          setImgUrl(imageUrl);
        });
      };
      onUploaded(info.file.response?._id as string, callback);
    }
  };

  async function customRequest({
    // onProgress,
    // onError,
    onSuccess,
    data,
    filename,
    file,
    // withCredentials,
    // action,
    headers,
  }: RcCustomRequestOptions) {
    const formData = new FormData();

    if (data) {
      Object.keys(data).forEach(key => {
        formData.append(key, (data as Dictionary<string>)[key]);
      });
    }

    formData.append(filename, file);

    let err: MyError | undefined;
    let res: File | Icon | undefined;

    if (imgUrl && imageId) {
      formData.append('imageId', imageId);
      [err, res] = await ImageService.update(
        formData, headers as Dictionary<string>
      );
    } else {
      [err, res] = await ImageService.add(
        formData, headers as Dictionary<string>
      );
    }

    if (!res || err) {
      message.error(`Failed to upload ${err?.error}`);
    } else {
      onSuccess(res, file);
    }
  }

  const uploadButton = (
    <div className='image-upload'>
      {loading ? <LoadingOutlined /> : <PlusOutlined />}
    </div>
  );

  const renderUpload = () => {
    if (!loading && imgUrl)
      return (
        <>
          <img
            src={imgUrl}
            alt="avatar"
            style={{ opacity: '0.5', width: '100%' }}
          />
          <PlusOutlined />
        </>
      );
    return uploadButton;
  };

  return (
    <StyledUpload
      name={name}
      listType="picture-card"
      onChange={handleChange}
      showUploadList={false}
      beforeUpload={beforeUpload}
      customRequest={customRequest}
    >
      <Wrapper height={height}>
        {renderUpload()}
      </Wrapper>
    </StyledUpload>
  );
}

export default ImageUpload;
