import React, { useEffect, useRef, useCallback } from 'react';
import { DashboardOutlined } from '@ant-design/icons';
import styled, { keyframes } from 'styled-components';
import { ShowerState } from '../../assets/const';

interface AtHeader {
  atHeader: boolean;
}

const Wrapper = styled.div<AtHeader>`
  position: relative;
  height: ${props => props.atHeader ? '100%' : `calc(${props.theme.font.sm}*3)`};
  width: ${props => props.atHeader ? '100%' : `calc(${props.theme.font.sm}*3 )`};
  font-weight: ${props => props.atHeader ? '700' : '400'};
  color: white;
  transition: all 0.5s linear;
  z-index: 1;
  cursor: pointer;
  > span {
    position: absolute;
    font-size: ${props => props.theme.font.sm};
    z-index: 3;
    color: white;
    transform: translateX(-50%);
    span {
      font-size: ${props => props.theme.font.sm};
    }
  }
`;

const spin = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(720deg);
  }
`;

const Water = styled.div<AtHeader>`
  position: absolute;
  background: gray;
  height: 100%;
  width: 100%;
  outline: 0;
  overflow: hidden;
  border-radius: ${props => props.atHeader ? '0%' : '25%'};
`;

const WaterLevel = styled.div<AtHeader>`
  position: absolute;
  bottom: -75%;
  left: -50%;
  height: ${props => props.atHeader ? '180px' : '200%'};
  width: ${props => props.atHeader ? '180px;' : '200%'};
  background-color: #434343;
  border-radius: 35%;
  animation: ${spin} 10s linear infinite;
  transition: all 0.3s linear;
`;

const waterLevelHeight = 180;
const waterLevelStep = 80;
const waterLevelOffset = waterLevelHeight - waterLevelStep;

const waterLevelHeightLarge = 270;
const waterLevelStepLarge = 100;
const waterLevelOffsetLarge = waterLevelHeightLarge - waterLevelStepLarge;

function VolumeCountDown({
  active,
  easy,
  atHeader,
  volume,
  setVolume,
  volumeCountdown,
  showerState,
  setShowerState,
  internval,
}: {
  atHeader: boolean;
  easy: boolean;
  internval: number;
  active: boolean;
} & VolumeCountdownControl & ShowerControl<ShowerState>) {
  const waterLevel = useRef<null | HTMLDivElement>(null);

  /**
   * Only countdown when
   * 1. shower started
   * 2. is active -> only one of the timer/volume countowns could be active
   */
  const shouldCoundDown = useCallback(() => {
    if (
      !active ||
      showerState === ShowerState.Inited
    ) return false;
    return true;
  }, [active, showerState]);

  /**
   * Adjust water volume level display
   */
  const updateWaterLevelHeight = useCallback(() => {
    if (!waterLevel.current) return;

    /** 
     * The height is given by its bottom value
     * there are 2 different height of water level display
     * 1. large height
     * 2. normal height
     */
    const waterBottomLarge =
      `-${(volumeCountdown - volume) / volumeCountdown * waterLevelStepLarge
      + waterLevelOffsetLarge}%`;
    const waterBottom =
      `-${(volumeCountdown - volume) / volumeCountdown * waterLevelStep
      + waterLevelOffset}%`;

    // only display large height when it is at header with eazy mode
    if (atHeader && !easy) {
      waterLevel.current.style.bottom = waterBottomLarge;
    } else {
      waterLevel.current.style.bottom = waterBottom;
    }
  }, [waterLevel, volumeCountdown, volume, atHeader, easy]);

  /**
   * Update Water Level
   */
  const updateWaterLevel = useCallback(() => {
    // no update if shower ended 
    // no update if it is atHeader
    // (countDown at header is just for display)
    if (showerState === ShowerState.End || !atHeader) return;

    // end shower if all water volume used 
    if (volume === 0) {
      setShowerState(ShowerState.End);
      return;
    }

    // here should calulate the new volume and update
    setVolume(current => current - 1);

  }, [showerState, atHeader, volume, setShowerState, setVolume]);

  useEffect(() => {
    updateWaterLevelHeight();
    if (!shouldCoundDown()) return () => { };

    // internvally update water volume & animation
    const to = setTimeout(() => {
      updateWaterLevelHeight();
      updateWaterLevel();
    }, internval);

    return () => clearTimeout(to);
  }, [
    shouldCoundDown,
    updateWaterLevelHeight,
    updateWaterLevel,
    internval
  ]);

  return (
    <Wrapper
      className={atHeader ? '' : 'volume-start-btn-wapper'}
      atHeader={atHeader}
      onClick={() => {
        // disable start button on header
        if (atHeader || !active) return;

        // start timer
        if (showerState === ShowerState.Inited) {
          setShowerState(ShowerState.Started);
        }
      }}
    >
      <Water
        atHeader={atHeader}
      >
        <WaterLevel
          atHeader={atHeader}
          ref={waterLevel}
        />
      </Water>
      {/* Time display) */}
      <span>
        {volume}L
        <DashboardOutlined style={{ display: 'block' }} />
      </span>
    </Wrapper>
  );
}

export default VolumeCountDown;
