/* eslint-disable no-underscore-dangle */
import http from './http';

export default class PecsService {
  static api = 'pecs';

  static async get():
  Promise<[undefined, Pecs[]] | [MyError, undefined]> {
    const request = {
      url: `${PecsService.api}`,
      requireAuth: true
    };

    try {
      const res = await http(request);
      const data: Pecs[] = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async add(pecs: { imageId: string, name: string })
    : Promise<[undefined, Pecs] | [MyError, undefined]> {
    const request = {
      url: `${PecsService.api}`,
      options: {
        method: 'POST',
        body: JSON.stringify(pecs)
      },
      requireAuth: true
    };

    try {
      const res = await http(request);
      const data: Pecs = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async update(pecs: { id: string, imageId: string, name: string })
    : Promise<[undefined, Pecs] | [MyError, undefined]> {
    const request = {
      url: `${PecsService.api}`,
      options: {
        method: 'PUT',
        body: JSON.stringify(pecs)
      },
      requireAuth: true
    };

    try {
      const res = await http(request);
      const data: Pecs = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async delete(pecsToDelete: { id: string, imageId: string })
    : Promise<[undefined, Pecs] | [MyError, undefined]> {
    const request = {
      url: `${PecsService.api}/${pecsToDelete.id}`,
      options: {
        method: 'DELETE',
        body: JSON.stringify({ imageId: pecsToDelete.imageId })
      },
      requireAuth: true
    };

    try {
      const res = await http(request);
      const data: Pecs = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }
}
