import React from 'react';
import styled from 'styled-components';
import { Layout } from 'antd';
import { BasicProps } from 'antd/lib/layout/layout';
import { useClock } from '../../hooks/index';
import { formatDate } from '../../utils/index';
import TimeCountdown from '../countDown/timeCountDown';
import VolumeCountdown from '../countDown/volumeCountDown';
import UserSeletcor from './userSelector';
import { StartMode, ShowerState, CONST } from '../../assets/const';

const Header = styled(Layout.Header)`
  position: relative;
  color: white;
  width: 100%;
  margin: auto auto;
  box-shadow: 0 0 10px #000000;
  font-weight: 800;
  background-color: ${props => props.theme.color.main};
  display: flex;
  height: ${props => props.theme.header.height};
  transition: all 0.3s ease-in-out;
  .app-clock {
    font-size: ${props => props.theme.font.sm};
    margin: auto;
  }
  .app-title {
    margin: auto;
    position: relative;
    flex-grow: 1;
    color: white;
    text-align: center;
    font-size: ${props => props.theme.font.sm};
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: bold;
    letter-spacing: 0.0075rem;
    /* right: -100px; */
  }
  .app-mode-btn {
    position: absolute;
    background: rgba(255,255,255,0.4);
    border: none;
    font-size: 1.4rem;
    right: 230px;
    width: 90px;
    font-weight: bold;
    height: 100%;
    &:focus {
      outline: 0 !important;
    }
    cursor: pointer;
  }
`;

const TimerWrapper = styled.div`
  position: absolute;
  right: 320px;
  width: 90px;
  height: 100%;
  top: 0;
  overflow: hidden;
`;

interface Props {
  setMode: React.Dispatch<React.SetStateAction<boolean>>;
  easy: boolean;
  finished: boolean;
  startMode: StartMode;
}

function AppHeader({
  setMode,
  startMode,
  easy,
  timer,
  timerCountdown,
  finished,
  setVolume,
  volume,
  volumeCountdown,
  showerState,
  setShowerState,
  ...props
}:
BasicProps &
Props &
VolumeCountdownControl &
TimeCountdownControl &
ShowerControl<ShowerState>
) {
  const date = new Date();
  const clock = useClock(date);

  return (
    <Header {...props}>
      <p className="app-clock">{formatDate(date)}</p>
      <h6 className="app-title"> Smart Shower </h6>
      <p className="app-clock">{clock.toLocaleTimeString()}</p>
      <UserSeletcor
        showerState={showerState}
        setShowerState={setShowerState}
      />
      <TimerWrapper>
        {startMode === StartMode.Time &&
          <TimeCountdown
            active={startMode === StartMode.Time}
            timer={timer}
            timerCountdown={timerCountdown}
            atHeader={true}
            showerState={showerState}
            setShowerState={setShowerState}
          />}
        {startMode === StartMode.Volume &&
          <VolumeCountdown
            active={startMode === StartMode.Volume}
            internval={CONST.VOLUME_CHECK_INTERNVAL}
            easy={easy}
            setVolume={setVolume}
            volume={volume}
            volumeCountdown={volumeCountdown}
            showerState={showerState}
            setShowerState={setShowerState}
            atHeader={true}
          />}
      </TimerWrapper>
      <button
        type="button"
        className="app-mode-btn"
        onClick={() => setMode(!easy)}
      >
        {easy ? 'Normal' : 'Easy'}
      </button>
    </Header>
  );
}

export default AppHeader;
