/* eslint-disable react/require-default-props */
import React, { useState, ReactNode } from 'react';
import styled, { keyframes } from 'styled-components';

const shrinkBounce = keyframes`
  0%{
    transform: scale(1);
  }
  33%{    
    transform: scale(.85);
  }
  100%{
    transform: scale(1);    
  }
`;

const check = keyframes`
  0%{
    width: 0;
    height: 0;
    border-color: #212121;
    transform: translate3d(0,0,0) rotate(45deg);
  }
  33%{
    width: .8em;
    height: 0;
    transform: translate3d(0,0,0) rotate(45deg);
  }
  100%{    
    width: .8em;
    height: 1.2em;    
    border-color: #212121;
    transform: translate3d(0,-.5em,0) rotate(45deg);
  }
`;

const ToDoInput = styled.input.attrs({
  type: 'checkbox'
})`
  height: 0;
  width: 0;
  &:checked + label > span {
    border: 1em solid #FFEB3B;
    animation: ${shrinkBounce} 200ms cubic-bezier(.4,.0,.23,1);
    &:before{
      content: "";
      position: absolute;
      top: 1.2em;
      left: .4em;
      border-right: 3px solid transparent;
      border-bottom: 3px solid transparent;
      transform: rotate(45deg);
      transform-origin: 110% 50%;
      animation: ${check} 125ms 250ms cubic-bezier(.4,.0,.23,1) forwards;
    }
  }
`;

const ToDoLabel = styled.label`
  position: relative;
  display: flex;
  margin-bottom: .6em;
  align-items: center;
  color: #9e9e9e;
  transition: color 250ms cubic-bezier(.4,.0,.23,1);
`;

const TodoCheckbox = styled.span`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1em;
  min-width: 2em;
  min-height: 2em;
  width: 2em;
  height: 2em;
  background: white;
  border: 2px solid #9E9E9E;
  border-radius: 2px;
  cursor: pointer;  
  transition: all 250ms cubic-bezier(.4,.0,.23,1);
`;

interface CheckboxProps {
  id: string;
  className?: string;
  onCheck?: (() => void);
  children?: ReactNode;
}

const ToDoCheckbox = ({
  id,
  className,
  onCheck,
  children
}: CheckboxProps) => {
  const [checked, setChecked] = useState(false);

  const handleCheck = () => {
    if (checked) return;
    setChecked(true);
    if (!onCheck) return;
    setTimeout(onCheck, 500);
  };

  return (
    <>
      <ToDoInput
        id={id}
        checked={checked}
        onChange={()=>{}}
      />
      <ToDoLabel htmlFor={id}>
        <TodoCheckbox
          className={className}
          onClick={() => {
            handleCheck();
          }}
          onTouchStart={(e) => {
            handleCheck();
          }}
        />
        {children}
      </ToDoLabel>
    </>
  );
};

export default ToDoCheckbox;
